export default {
  path: "/sales",
  component: () => import("@/views/appNav.vue"),
  meta: {
    requireAuth: true
  },
  children: [
    {
      path: "sales_open_order",
      name: "sales_open_order",
      component: () => import(
        "@/components/ServiceStation/sales/salesOrder/salesOpenOrder.vue"
      ),
      meta: {
        _meauname: "销售开单",
        keepAlive: true
      }
    },
    {
      path: "service_station_sales_outtock_detail",
      name: "service_station_sales_outtock_detail",
      component: () => import(
        "@/components/ServiceStation/sales/outbound/outboundDetail.vue"
      ),
      meta: {
        _meauname: "销售出库明细表",
        keepAlive: true
      }
    },
    {
      path: "service_station_sales_outstock_list",
      name: "service_station_sales_outstock_list",
      component: () => import(
        "@/components/ServiceStation/sales/outbound/outboundList.vue"
      ),
      meta: {
        _meauname: "出库列表",
        keepAlive: true
      }
    },
    {
      path: "service_station_sales_outstock_list/service_station_sales_outstock_add",
      name: "service_station_sales_outstock_list/service_station_sales_outstock_add",
      component: () => import(
        "@/components/ServiceStation/sales/outbound/outboundAdd.vue"
      ),
      meta: {
        _meauname: "出库列表",
        keepAlive: true
      }
    },
    {
      path: "service_station_sales_outstock_list/service_station_sales_outstock_see",
      name: "service_station_sales_outstock_list/service_station_sales_outstock_see",
      component: () => import(
        "@/components/ServiceStation/sales/outbound/outboundSee.vue"
      ),
      meta: {
        _meauname: "出库列表",
        keepAlive: true
      }
    },
  ]
}

