import md5 from "js-md5";
import Qs from "qs";
const unchangeable = "jybd20180331";
let Base64 = require("js-base64").Base64;
 function serverMoudle(params) {
  if (params) {
    for (let element in params) {
      //防止直接传递数组
      if (params[element] instanceof Array) {
        params[element] = params[element].join(",");
      }
    }
  }
  //obj sort序列化处理 反解析格式
  var newUrl = null;
  if (params || "") {
    if (Object.keys(params).length) {
      newUrl =
        decodeURIComponent(Qs.stringify(objKeySort(params))) +
        "&key=" +
        unchangeable;
      return md5(newUrl);
    } else {
      newUrl = "key=" + unchangeable;
      return md5(newUrl);
    }
  } else {
    newUrl = "key=" + unchangeable;
    return md5(newUrl);
  }
}
const objKeySort = obj => {
  //排序的函数
  var newkey = Object.keys(obj).sort();
  var newObj = {}; //创建一个新的对象，用于存放排好序的键值对
  for (var i = 0; i < newkey.length; i++) {
    //遍历newkey数组
    if ([null, undefined].includes(obj[newkey[i]])) {
      obj[newkey[i]] = "";
    }
    newObj[newkey[i]] = obj[newkey[i]]; //向新创建的对象中按照排好的顺序依次增加键值对
  }
  return newObj; //返回排好序的新对象
};
const compilePsworld = data => {
  data._userp = Base64.encode(data._userp + getNowFormatDate());
  return data;
};
const getNowFormatDate = () => {
  let date = new Date();
  let seperator1 = "-";
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  let currentdate = year + seperator1 + month + seperator1 + strDate;
  return currentdate;
};

const Logout=()=>{
    var box = ['role_id', 'titName', 'site', 'department_id', 'affiliated_company_name', 'purchase_center_company_name', 'company_name', 'hasAuditorStatusPrint', 'business_sn', 'employee_name', 'is_docking', 'FDBName_business', 'realname', 'purchase_center_company_id', 'loglevel:webpack-dev-server', 'affiliated_company_id', 'user_id', 'Documents_operation', 'employee_id', 'login_mac', 'company_admin_id', 'last_login_time', 'department_name', 'company_str', 'company_id', 'token', 'startMenu']
    box.forEach(val => {
      localStorage.removeItem(val);
    })
}

export{
    serverMoudle,
    compilePsworld,
    Logout
}