<template>
  <el-upload class="uploadFile" :headers='headers' :limit='limit' :on-exceed='onExceed' :action="$axios.defaults.baseURL ? $axios.defaults.baseURL + url:  url" ref='upload' :before-upload='beforeUploadBefore' :data='dataParams' :on-error='error' :on-success='onSuccess' :before-remove='beforeRemove' :on-preview="handlePreview" :on-remove="handleRemove" :file-list="list" list-type="picture">
    <slot slot="default"></slot>
    <slot slot="tip" name='tip'>
    </slot>
    <vxe-modal v-model="isShowImg" width='1200' title='图片预览' :esc-closable='true' :mask-closable='true' destroy-on-close height="800" min-width="500" min-height="100" resize remember storage>
      <div style='text-align:center'>
        <img :src="imgUrl" alt="">
      </div>
    </vxe-modal>
  </el-upload>
</template>

<script>
import { serverMoudle } from "@/utils/fetch";

export default {
  name: "cupload",
  data() {
    return {
      list: [],
      imgUrl: "",
      headers: {
        token: localStorage.getItem("token"),
      },
      timeStamp: new Date().getTime(),
      isShowImg: false,
    };
  },
  computed: {
    dataParams() {
      let datas = Object.assign({}, this.extendParams, {
        timeStamp: this.timeStamp,
      });
      datas.sign = serverMoudle(datas);
      return datas;
    },
  },
  props: {
    value: {
      // 回显字段
      // type: str,
      // default: function() {
      //   return [];
      // }
    },
    limit: {
      type: Number,
    },
    url: {
      type: String,
      default: "/v2/open/Upload/uploadImg",
    },
    beforeRemove: {
      type: Function,
      default: () => true,
    },
    extendParams: {
      type: Object,
      default() {
        return {};
      },
    },
    returnStr: {
      type: String,
      default: "",
    },
    allowType: {
      // 允许的类型
      type: Array,
      default: () => [],
    },
    sizeLimit: {
      type: Array || Number,
      default: () => {
        return [
          // {
          //   type: ["jpg"],
          //   size: 0.01
          // }
        ];
      },
    },
    beforeUpload: {
      // 外部拦截
      type: Function,
      default: () => {
        return true;
      },
    },
  },
  // watch: {
  //   value (newVal) {
  //     this._initList(newVal);
  //   }
  // },
  mounted() {
    this._initList(this.value);
  },
  methods: {
    onExceed() {
      this.$message.error("只能上传" + this.limit + "个文件");
    },
    // 初始化
    _initList(arr) {
      if (typeof arr === "string" && arr.length > 5) {
        arr = arr.split(",");
      }
      if (Array.isArray(arr)) {
        this.list = arr.map((val) => {
          if (typeof val === "object") {
            return {
              url: val.url,
              image: val.image ? val.image : val.url,
              name: val.name ? val.name : "文件", //"图片" + key
            };
          } else {
            return {
              url: val,
              image: val,
              name: "文件", //"图片" + key
            };
          }
        });
      }
      this.listChange(this.list);
    },
    _initListTwo(arr) {
      this.list = arr;
      this.listChange(this.list);
    },
    // 上传失败
    error() {
      this.$message.error("上传失败,请稍后再试");
    },
    // // 上传
    // httpRequest (data) {
    //   var params = new FormData()
    //   params.append('filename',data.file)
    //   this.$axios({
    //     method: 'post',
    //     url: 'upload/UploadFile/addAllFile',
    //     data: params
    //   }).then((data) => {
    //     console.log(data)
    //   })
    // },
    isImg(str) {
      var result = str.substring(str.lastIndexOf(".") + 1);
      if (
        result == "jpg" ||
        result == "jpeg" ||
        result == "png" ||
        result == "bmp"
      ) {
        return true;
      }
      return false;
    },
    isPDF(str) {
      var result = str.substring(str.lastIndexOf(".") + 1);
      if (result == "pdf") {
        return true;
      }
      return false;
    },
    beforeUploadBefore(file) {
      return new Promise((resolve, reject) => {
        if (this.beforeUploadAction(file)) {
          this.timeStamp = new Date().getTime();
          this.$nextTick(() => {
            resolve();
          });
        } else {
          reject();
        }
      });
    },
    // 上传之前
    beforeUploadAction(file) {
      if (!this.beforeUpload()) {
        return false;
      }
      var MB = 1024 * 1024;
      var result = file.name
        .substring(file.name.lastIndexOf(".") + 1)
        .toLocaleLowerCase();
      if (
        this.allowType.length &&
        !this.allowType.some((val) => val === result)
      ) {
        this.$message.error(
          "请上传" + this.allowType.join("、") + "格式的文件"
        );
        return false;
      }
      if (this.sizeLimit) {
        if (Array.isArray(this.sizeLimit)) {
          for (var i = 0; i < this.sizeLimit.length; i++) {
            if (
              this.sizeLimit[i].type.some((val) => val === result) &&
              file.size > this.sizeLimit[i].size * MB
            ) {
              this.$message.error(
                result + "文件请上传" + this.sizeLimit[i].size + "兆内"
              );
              return false;
            }
          }
        } else if (file.size > this.sizeLimit * MB) {
          this.$message.error(result + "文件请上传" + this.sizeLimit + "兆内");
          return false;
        }
      }
      return true;
    },
    onSuccess(response, file, fileList) {
      if (response.code === 200) {
        this.listChange(fileList);
      } else {
        fileList.pop();
        this.$message.error(response.msg);
      }
    },
    // 列表改变
    listChange(fileList) {
      this.list = this.$publicFution.deepCopy(fileList);
      this.$emit(
        "input",
        this.list
          .map((val) => {
            if (val.response) {
              // 上传的url
              if (this.returnStr) {
                var result = val;
                this.returnStr.split(".").forEach((twoVal) => {
                  result = result[twoVal];
                });
                return result;
              } else {
                return val.response ? val.response.data : val.name;
              }
            } else {
              // 回显的url
              return val.image;
            }
          })
          .join(",")
      );
      this.$emit("listChange", this.list);
    },
    handleRemove(file, fileList) {
      this.listChange(fileList);
      return this.beforeRemove(file, fileList);
    },
    handlePreview(file) {
      this.imgUrl = file.url;
      if (this.isPDF(file.name)) {
        window.open(this.imgUrl, "_blank");
      } else {
        this.isShowImg = true;
      }
    },
  },
};
</script>

<style lang='less' scoped>
.uploadFile {
  /deep/ .el-upload-list--picture {
    &::after {
      content: "";
      display: block;
      clear: both;
    }
    .el-upload-list__item {
      width: 250px;
      float: left;
    }
  }
}
</style>
