<template>
  <!-- erp批次-->
  <el-select v-model="value" placeholder="请选择批次" :loading='loading' :clearable="clearable" @clear='change("")' :disabled='disabled' @focus="getList('')" @change="change" filterable maxlength="100">
    <el-option v-for="item in list" :key="item.batch_id" :label="item.batch" :value="item.batch_id">
      <span> {{ item.batch }} </span>
      <template slot='empty'>
        1111
      </template>
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "erpBatch",
  data() {
    return {
      list: [],
      loading: false,
      isChange: false // 是否是change引起的变动
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelValue: {
      type: String,
      default: "batch_id"
    },
    modelLabel: {
      type: String,
      default: "batch"
    },
    url: {
      type: String,
      default: "/open/GoodsBatch/getGoodsBatch"
    }
    // resultStr: {
    //   type: String,
    //   default: "company_list"
    // }
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            batch: this.value,
            batch_id: this.fatherParams[this.modelLabel]
          }
        ];
      }
    },
    _setList(list) {
      this.list = this.list.concat(list);
    },
    getList(query) {
      this.loading = true;
      this.searchParams.batch = query;
      this.searchParams.hideLoding = true;
      this.list = [];
      this.$axios({
        method: "get",
        url: this.url,
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
        this.loading = false;
      });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.batch;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

