<template>
  <!-- erp货位 -->
  <el-select v-model="value" placeholder="请选择货位" :disabled='disabled' clearable @clear='change("")' filterable @focus="getList('')" remote @change="change">
    <el-option v-for="item in list" :key="item.allocation_id" :label="item.allocation_name" :value="item.allocation_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getServiceAllocation",
  data() {
    return {
      list: [],
      isChange: false // 是否是change引起的变动
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      // 查询条件对象
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "allocation_name"
    },
    modelValue: {
      type: String,
      default: "allocation_id"
    }
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            allocation_id: this.value,
            allocation_name: this.fatherParams[this.modelLabel]
          }
        ];
      }
    },
    getList(query) {
      // this.searchParams.allocation_name = query;
      this.searchParams.hideLoding = true;
      this.$axios({
        url: "/open/Allocation/getAllocationList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.allocation_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

