<template>
  <div>
    <el-autocomplete ref='autocomplete' v-model="scopes.row.goods_name" popper-class="scrollClass" :fetch-suggestions="remoteGoods" placeholder="请输入商品" @select="(value)=>{changeGoods(scopes.row,scopes.$index,value)}">
      <template slot-scope="{ item }">
        <ul v-if="item.disabled" @click.stop.prevent="" class="goods_ul goods_ul3 title  warehouseTitle">
          <li>商品编号</li>
          <li>商品名称</li>
          <li>可用库存</li>
          <li>在库库存</li>
        </ul>
        <div v-else-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          <i class='icon el-icon-loading'></i> 拼命加载中
        </div>
        <div v-else-if='item.isFinish' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          已经到底了
        </div>
        <ul class="goods_ul  goods_ul3" v-else>
          <li style="color: #8492a6; font-size: 13px">{{ item.goods_id }}</li>
          <li>{{item.value=item.goods_name }}</li>
          <li>{{ item.available_stocks }}</li>
          <li>{{ item.stocks }}</li>
        </ul>
      </template>
    </el-autocomplete>
  </div>
</template>

<script>
import { Debounce } from "common/utils/public.js";

export default {
  name: "JybdGoodslist",
  props: ["goodsopthion", "scopes", "fatherGoods"],
  data() {
    return {
      goodslist: [],
      page: 1,
      isLoading: false, // 是否加载中
      isFinish: false, // 是否滚动到底
      query: "",
      source: null,
      loading:false,
      callback: () => {},
      done: false
    };
  },
  methods: {
    // 滚动加载
    scroll() {
      let box = document.getElementsByClassName(
        "el-autocomplete-suggestion__wrap"
      );
      var that = this;
      for (let i = 0; i < box.length; i++) {
        box[i].onscroll = function() {
          if (
            box[i].scrollTop + box[i].clientHeight + 200 >
              box[i].children[0].clientHeight &&
            !that.isLoading &&
            !that.isFinish
          ) {
            that.getList(that.query);
            that.goodslist.push({
              loading: true
            });
            that.page++;
            that.isLoading = true;
          }
        };
      }
    },
    // 选择商品
    remoteGoods: Debounce(function(query, callback) {
      this.query = query;
      this.page = 1;
      this.isFinish = false;
      this.goodslist = [
        {
          goods_id: "编号",
          goods_name: "商品名称",
          disabled: true
        }
      ];
      this.getList(query, callback);
      this.scroll();
    }, 500),
    getList(query, callback) {
      let params = {
        company_id: this.goodsopthion.company_id,
        customer_id: this.goodsopthion.customer_id,
        order_id: this.goodsopthion.order_id || 0,
        warehouse_id: this.goodsopthion.warehouse_id || 0,
        bill_type: this.goodsopthion.bill_type || 0,
        page: this.page,
        goods_name: query
      };
      let api = this.goodsopthion.Method
        ? this.goodsopthion.Method
        : "getGoodsSnList";
      this.$axMethod[api](params, this).then(res => {
        if (this.isLoading) {
          this.goodslist.pop(); // loading需要删除一行
        }
        if (res.data.code == 200) {
          this.goodslist.push(...res.data.data);
          if (res.data.data.length < 30) {
            this.isFinish = true;
            this.goodslist.push({
              isFinish: true
            });
          }
          callback && callback(this.goodslist);
        }
        this.isLoading = false;
      });
    },
    changeGoods(row, index, value) {
      if (this.fatherGoods) {
        this.fatherGoods(row, this.scopes.$rowIndex, value);
      }
      this.$emit("changeGoods", row, index, value);
    }
  }
};
</script>

