export default {
  methods: {
    initEdit() {
      // 复选框配置
      var selectConfig = {
        showHeader: true,
        reserve: true,
        checkMethod: event => { // 复选框再
          return this.rowClass(event, true);
        }
      };
      this.select_Config = Object.assign(selectConfig, this.selectConfig)
      // 可编辑配置
      var editConfig = {
        trigger: "click",
        mode: "cell",
        showStatus: false,
        showIcon: true,
        activeMethod: this.isEdit, // 是否可以编辑
        autoClear: false // 不失去焦点 编辑其他行失去焦点
      };
      this.edit_Config = Object.assign(editConfig, this.editConfig);
    }
  },
}