import Sortable from "sortablejs";
// import { Message } from 'element-ui'

export default {
  methods: {
    async initDrag () {
      await this.$nextTick()
      const $table = this.$refs.vxeGrid
      this.sortable = Sortable.create($table.$el.querySelector('.body--wrapper>.vxe-table--header .vxe-header--row'), {
      handle: '.vxe-header--column:not(.col--fixed)',
      onEnd: ({ item, newIndex, oldIndex }) => {
        const { fullColumn, tableColumn } = $table.getTableColumn()
          const targetThElem = item
          const wrapperElem = targetThElem.parentNode

        // if (fullColumn[newIndex].type == '' || fullColumn[oldIndex].type == 'checkbox') {
        //   // 错误的移动
        //   if (newIndex > oldIndex) {
        //     wrapperElem.insertBefore(targetThElem, wrapperElem.children[oldIndex])
        //   } else {
        //     wrapperElem.insertBefore(wrapperElem.children[oldIndex], targetThElem)
        //   }
        //   return Message.error('移动失败')
        // }
        // 转换真实索引
        const oldColumnIndex = $table.getColumnIndex(tableColumn[oldIndex])
        const newColumnIndex = $table.getColumnIndex(tableColumn[newIndex])
        // 移动到目标列
        const currRow = fullColumn.splice(oldColumnIndex, 1)[0]
        fullColumn.splice(newColumnIndex, 0, currRow)
        
        // 长久化存储
        const config = []
        fullColumn.forEach(item => {
          config.push({
            title: item.title,
            type: item.type,
            property: item.property
          })
        })
        window.localStorage.setItem(`vtable-${this.rowId}-${this.$route.fullPath}`, JSON.stringify(config))

        $table.loadColumn(fullColumn)
      }
    })
    }
  },
  beforeDestroy () {
    if (this.sortable) {
      this.sortable.destroy()
    }
  }
}