// 更新版本处理api变化

const mapFunction = {
  "setSelection": "setCheckboxRow", //设置选中
  "removeSelecteds": "removeCheckboxRow", //删除选中
  "getSelectRecords": "getCheckboxRecords",// 获取选中
}

export default {
  methods: {
    // 代理调用vxe的方法
    _proxy() {
      var argumentsOne = mapFunction[arguments[0]] ? mapFunction[arguments[0]] : arguments[0]
      // 需要更新合计行的方法
      if (
        argumentsOne === "loadData" ||
        argumentsOne === "insertAt" ||
        argumentsOne === "removeSelecteds" ||
        argumentsOne === "remove"
      ) {
        setTimeout(() => {
          this.$emit('tableUpdate')
          if (this.$parent.vxeTableData) {
            // vxeTableData 为表格的对象
            this.$parent.vxeTableData.length = this.$refs.vxeGrid.getTableData().fullData.length;
          }
          this.$refs.vxeGrid.updateFooter();
        }, 50);
      }
      // 拦截校验方法
      if (argumentsOne === 'validate') {
        return new Promise((resolve, reject) => {
          var result = this.$refs.vxeGrid[argumentsOne].apply(
            null,
            Array.prototype.slice.call(arguments, 1)
          );
          result.then((result) => {
            resolve(result)
          }).catch((errMap) => {
            reject(errMap)
            if (errMap) {
              for (var key in errMap) {
                this.$message.warning(errMap[key].rule.message)
              }
            }
          })
        })
      }
      return this.$refs.vxeGrid[argumentsOne].apply(
        null,
        Array.prototype.slice.call(arguments, 1)
      );
    },
    // 复制行 插入行
    _copyRow(row, records) {
      var index = this._proxy('$getRowIndex', row);
      var box = this._proxy('getTableData').fullData.concat([]);
      var copyRow = this.$publicFution.deepCopy(row);
      delete copyRow[this.rowId];
      if (index < box.length - 1) {
        return this._proxy('insertAt', records ? records : copyRow, box[index + 1])
      } else {
        return this._proxy('insertAt', records ? records : copyRow, -1)
      }
    },
    // 数据代理获取数据
    _getData(index, page) {
      if (this.$parent.handleSelectionChange) {
        // this._proxy('clearCheckboxRow')
        if (this.$parent.handleSelectionChange) {
          this.$parent.handleSelectionChange([]);
        }
      }
      if (this.isShow) {
        this.$nextTick(() => {
          this._proxy('commitProxy', 'query', index, page)
        });
      }
    },
    // 更新表尾合计行
    _updateFooter() {
      this._proxy('updateFooter')
    },

    // 初始化合计
    initFooter() {
      var that = this;
      var footerMethods;
      if (this.footerMethod) {
        footerMethods = this.footerMethod;
        this.showFooter = true;
      } else if (this.totalArray.length > 0) {
        footerMethods = function ({
          columns
        }) {
          return [
            columns.map((column, columnIndex) => {
              if (columnIndex === that.totalIndex) {
                return "合计";
              }
              if (that.totalArray.includes(column.property)) {
                var num;
                if (that.$refs.vxeGrid) {
                  num = that.$utils.sum(
                    that._proxy("getTableData").fullData,
                    column.property
                  );
                } else {
                  num = 0;
                }
                return Number.isInteger(num) ?
                  that.$publicFution.priceFormatNone(num, 0) :
                  that.$publicFution.priceFormatNone(num.toFixed(2));
              }
              return null;
            })
          ];
        };
        this.showFooter = true;
      }
      this.footerMethods = footerMethods
    }
  }
}