<template>
  <!-- 客户下拉-->
  <el-select v-model="value" placeholder="请选择" filterable :disabled='disabled' @focus="getList" @change="change">
    <el-option v-for="item in list" :key="item" :label="item.customer_name" :value="item.customer_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "customer",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "customer_name"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (this.fatherParams && this.fatherParams[this.modelLabel]) {
        this.list.push({
          customer_id: this.value,
          customer_name: this.fatherParams[this.modelLabel]
        });
      }
    },
    getList() {
      this.list = [];
      this.$axios({
        url: "/purchase_center/Open/Customer/getCustomerList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      this.$emit("change", val);
    }
  }
};
</script>

