// 第一位不能为0，保留两位小数
const checkNumPot2 = (rule, value, callback) => {
  const reg = /(^[0-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
  if (!value && value !== 0) {
    return callback(new Error("请填写数字"));
  } else if (!reg.test(value)) {
    return callback(new Error("请填写大于等于0数字,最多2位小数"));
  } else {
    callback();
  }
};
const checkNum = (rule, value, callback) => {
  if (isNaN(value)) {
    return callback(new Error("请填写数字"));
  } else if (
    value.toString().split(".")[1] ?
      value.toString().split(".")[1].length > 2 :
      false
  ) {
    return callback(new Error("最多2位小数"));
  } else {
    callback();
  }
};
// // 身份证
const checkIdNum = (rule, value, callback) => {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
  if (!reg.test(value)) {
    return callback(new Error("证件号码不正确"));
  } else {
    callback();
  }
};

// 整数
const checkInterNum = (rule, value, callback) => {
  const reg = /^[0-9]*[1-9][0-9]*$/;
  if (!value) {
    return callback(new Error("请填写整数"));
  } else if (!reg.test(value)) {
    return callback(new Error("请输入整数"));
  } else {
    callback();
  }
};
//验证座机
const telephone = (rule, value, callback) => {
  const reg = /^([0-9]{3,4}-)?[0-9]{7,8}$/;
  if (!reg.test(value) && value != undefined && value != "") {
    return callback(new Error('请正确输入固定电话,格式为"区号-座机号码"'));
  } else {
    callback();
  }
};
//验证客服电话400-010-1231

const kfphone = (rule, value, callback) => {
  const reg = /^400(-\d{3,4}){2}$/;
  if (!reg.test(value) && value != undefined && value != "") {
    return callback(new Error('请正确输入客服电话,格式为"400-xxx-xxx"'));
  } else {
    callback();
  }
};
//验证客服电话400-010-1231

const bank_numberys = (rule, value, callback) => {
  const reg = /^[0-9]*[1-9][0-9]*$/;
  if (!value) {
    return callback(new Error("请正确输入银行卡号"));
  } else if (!reg.test(value)) {
    return callback(new Error("请正确输入银行卡号"));
  } else {
    callback();
  }
};

//输入为空
const kong = (rule, value, callback) => {
  if (!value) {
    return callback(new Error("输入不能为空"));
  } else {
    callback();
  }
};
//邮箱验证

const emailNew = (rule, value, callback) => {
  const reg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,5}$/;
  if (!reg.test(value)) {
    return callback(
      new Error("4~18个字符，可使用字母、数字、下划线，需以字母开头@xxx.com/cn")
    );
  } else {
    callback();
  }
};
const purchasePrice = (rule, value, callback) => {
  if (!value) {
    return callback(new Error("请输入采购价"));
  } else if (value < 0) {
    return callback(new Error("采购价不能小于0"));
  } else {
    callback();
  }
};

