<template>
  <!-- erp组织公司 -->
  <el-select v-model="value" :loading="loading" placeholder="请选择组织" :clearable="clearable" @clear='change("")' :disabled='disabled' @focus="getList('')" @change="change" filterable maxlength="100">
    <el-option v-for="item in list" :key="item.company_id" :label="item.company_name" :value="item.company_id">
      <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getServiceCompany",
  data() {
    return {
      list: [],
      loading: false,
      isChange: false, // 是否是change引起的变动
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      },
    },
    startRequest: {
      // 是否一开始就请求
      type: Boolean,
      default: false,
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      },
    },
    modelValue: {
      type: String,
      default: "company_id",
    },
    modelLabel: {
      type: String,
      default: "company_name",
    },
    url: {
      type: String,
      default: "/open/company/getErpCompanyList",
    },
    resultStr: {
      type: String,
      default: "company_list",
    },
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    },
  },
  mounted() {
    this.init();
    if (this.startRequest) {
      this.getList();
    }
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            company_id: this.value,
            company_name: this.fatherParams[this.modelLabel],
          },
        ];
      }
    },
    _setList(list) {
      this.list = this.list.concat(list);
    },
    getList(query) {
      this.loading = true;
      this.searchParams.company_name = query;
      this.list = [];
      this.$axios({
        method: "get",
        url: this.url,
        params: this.searchParams,
      }).then(({ data }) => {
        this.loading = false;
        let result = data.data;
        this.resultStr.split(".").forEach((val) => {
          if (val) {
            result = result[val];
          }
        });
        this.list = result;
        if (
          result instanceof Array &&
          this.url === "/open/company/getErpCompanyList"
        ) {
          // 请求公司列表额外处理
          result.unshift({
            company_name: data.data.company_name,
            company_id: data.data.company_id,
            is_choose: data.data.is_choose,
            type: 1,
          });
          this.list = result.filter((item) => {
            return item.is_choose == 1; // 是否可以被选择 1可以 0不可以
          });
        }
        var returnObj = {};
        if (this.value) {
          returnObj = this.list.filter(
            (item) => item[this.modelValue] === this.value
          )[0];
        }
        this.$emit("getList", this.list, returnObj);
      });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter((item) => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.company_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    },
  },
};
</script>

