<template>
  <!-- 下拉框渲染器 -->
  <div element-loading-spinner="el-icon-loading">
    <el-select ref='select' popper-class='selectPullDown' :class='popperClass' remote :loading="loading" v-model="scope.row[selectId]" placeholder="请选择" collapse-tags :multiple='extendParams.multiple' filterable @focus="getData" @change="selectChange" :clearable='clearable'>
      <ul class="warehouse-ul warehouse-ul2 warehouse-ul3">
        <li v-for="(val, key) in showObj" :key='key' v-if='val'>
          {{ val }}
        </li>
      </ul>
      <el-option v-for="(item,key) in list" :key="key" :label="item[itemObj.name]" :value='item[itemObj.id]'>
        <div v-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          <i class='icon el-icon-loading'></i> 拼命加载中
        </div>
        <div v-else-if='item.isFinish' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          已经到底了
        </div>
        <ul class="warehouse-ul  warehouse-ul3" v-if='showObj'>
          <li v-for="(val, key) in showObj" :key='key'>
            {{ item[key] }}
          </li>
        </ul>
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [], // 货位数组
      page: 1,
      loading:false,
      isLoading: false, // 是否加载中
      isFinish: false, // 是否滚动到底
      selectId: ""
    };
  },
  props: {
    clearable: {
      type: Boolean,
      default: true
    },
    modelKey: {
      // 下拉框 v-model 绑定的id
      type: String,
      default: ""
    },
    modelLabel: {
      type: String,
      default: ""
    },
    vxeParams: {
      // 请求参数
      type: Object,
      default: () => null
    },
    scope: {
      type: Object,
      default: () => {}
    },
    options: {
      // 如果不用列表是死的可以不用请求浏览器
      type: Array,
      default: () => []
    },
    isPage: {
      type: Boolean,
      default: false
    },
    // for 循环使用字段
    itemObj: {
      type: Object,
      default: () => {
        return {
          id: "value",
          name: "label"
        };
      }
    },
    returnString: {
      // 后台返回格式
      type: String,
      default: () => "data.data"
    },
    showObj: {
      // 展示label 和id
      type: Object,
      default: () => {
        return null;
      }
    },
    // 扩展参数
    extendParams: {
      type: Object,
      default: newObj => {
        var defaultObj = {
          multiple: false
        };
        return Object.assign({}, defaultObj, newObj);
      }
    },
    // 下拉框的class
    popperClass: {
      type: String,
      default: ''
    },
    url: {
      type: String,
      default: ""
    },
    change: {
      type: Function,
      default: function() {
        // console.log("请写change方法");
      }
    },
    dealList: {
      // 处理数据的方法
      type: Function,
      default: function() {
        // console.log("请写dealList方法");
      }
    }
  },
  created() {
    if (this.modelKey) {
      this.selectId = this.modelKey;
    } else {
      this.selectId = this.itemObj.id;
    }
    var box = this.$publicFution.deepCopy(this.scope.row);
    box[this.itemObj.id] = box[this.selectId];
    box[this.itemObj.name] =
      box[this.modelLabel ? this.modelLabel : this.itemObj.name];
    this.list = this.list.concat(this.options);
    if (this.options.length === 0) {
      // id等于0 名字没有 不回显
      if (box[this.itemObj.id] === 0 && !box[this.itemObj.name]) {
        this.scope.row[this.selectId] = "";
        return;
      }
      this.list.push(box); // 下拉框 v-model 绑定的id （不传取itemObj的id）
    }
  },
  methods: {
    // 滚动加载
    scroll() {
      let box = document.getElementsByClassName("selectPullDown");
      let currentSelect,
        that = this;
      for (var i = 0; i < box.length; i++) {
        if (box[i].style.display !== "none") {
          currentSelect = box[i];
        }
      }
      currentSelect = currentSelect.getElementsByClassName(
        "el-select-dropdown__wrap"
      )[0];
      currentSelect.onscroll = function() {
        if (
          currentSelect.scrollTop + currentSelect.clientHeight + 200 >
            currentSelect.children[0].clientHeight &&
          !that.isLoading &&
          !that.isFinish
        ) {
          that.page++;
          that.list.push({
            loading: true
          });
          that.getList();
          that.isLoading = true;
        }
      };
    },
    // 选择改变
    selectChange(value) {
      var result;
      if (Array.isArray(value)) {
        result = value
      } else {
        result = this.list.filter(obj => value === obj[this.itemObj.id])[0];
        result = result ? result : {}
        try {
          this.scope.row[this.scope.column.property] = result[this.itemObj.name];
        } catch (error) {}
      }
      this.change && this.change(this.scope, result, this.list);
      if (this.scope.$table) {
        this.scope.$table.updateStatus(this.scope);
      }
    },
    getData() {
      if (this.isPage === true) {
        // 分页逻辑
        this.list = [];
        this.page = 1;
        this.isFinish = false;
        setTimeout(() => {
          this.scroll();
        }, 50);
        this.getList();
      } else {
        if (this.$axMethod[this.url] || this.$api[this.url]) {
          this.list = [];
        }
        this.getList();
      }
    },
    // 获取列表
    getList() {
      console.log(this.$api)
      let params = this.$publicFution.deepCopy(this.vxeParams);
      if (this.isPage === true) {
        // 分页逻辑
        params.page = this.page;
        params.per_page = 30;
      }
      this.loading = true;
      if (this.$axMethod[this.url] || this.$api[this.url]) {
        var AjaxFunc = this.$axMethod[this.url]
          ? this.$axMethod[this.url](params)
          : this.$api[this.url](params);
        AjaxFunc.then(res => {
          this.loading = false;
          if (res.data.code === 200) {
            var resultBox = this.$publicFution.deepCopy(res);
            this.returnString.split(".").forEach(val => {
              // 不同的返回数据格式做不同的处理
              resultBox = resultBox[val];
            });
            if (!Array.isArray(resultBox)) {
              resultBox = [];
            }
            if (this.isPage === true) {
              if (this.isLoading) {
                this.list.pop(); // loading需要删除一行
              }
              this.list = this.list.concat(resultBox);
              if (res.data.data.last_page === res.data.data.current_page) {
                this.isFinish = true;
                this.list.push({
                  isFinish: true
                });
              }
              this.isLoading = false;
            } else {
              this.list = resultBox;
            }
            this.dealList(
              // 把列表返出去
              this.list,
              this.list.some(
                val => val[this.itemObj.id] === this.scope.row[this.selectId]
              )
            );
          }
        });
      } else {
        this.loading = false;
      }
    }
  }
};
</script>

<style lang='less' scoped>
/deep/ .el-loading-spinner {
  margin-top: -8px;
}
</style>