<template>
  <el-autocomplete ref="autoinput" clearable @clear='clearGoods' :disabled='disabled' autocomplete v-model="query" @blur="removeScroll" popper-class="goodsListContainer" :fetch-suggestions="remoteGoods" placeholder="请输入商品" @select="(value)=>{change(scopes,value)}">
    <template slot-scope="{ item }">
      <ul v-if="item.disabled" @click.stop.prevent="" class="goodsListItem goodsListTitle">
        <li v-for="(val, key) in showObj" :key='key'>
          {{ val }}
        </li>
      </ul>
      <div v-else-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
        <i class='icon el-icon-loading'></i> 拼命加载中
      </div>
      <div v-else-if='item.isFinish' @click.stop.prevent="" style='text-align: center;color:#409eff'>
        已经到底了
      </div>
      <ul class="goodsListItem" v-else>
        <li v-for="(val, key) in showObj" :key='key' :title="item[key]">
          {{ item[key] }}
        </li>
      </ul>
    </template>
  </el-autocomplete>
</template>

<script>
import { Debounce } from "common/utils/public.js";

export default {
  name: "vGoodsList",
  props: {
    vxeParams: {
      type: Object,
      default: () => {}
    },
    scopes: {
      type: Object,
      default: () => {}
    },
    keyObj: {
      // 如果对象的key  value 不是good_id  good_name 传这个参数
      type: Object,
      default: () => {
        return {
          label: "goods_name",
          value: "goods_id"
        };
      }
    },
    beforeGetList: {
      // 再请求接口之前校验当前是否可以请求接口
      type: Function,
      default: () => true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    updateArray: {
      // 默认赋值数组，选择商品后赋值
      type: Array,
      default: () => []
    },
    showObj: {
      // 商品显示行列
      type: Object,
      default: () => {
        return {
          goods_id: "商品编号",
          goods_name: "商品名称",
          factory_code: "工厂代码",
          available_stocks: "可用库存",
          stocks: "在库库存"
        };
      }
    },
    isFuzzy: {
      type: Boolean,
      default: false
    },
    returnString: {
      // 后台返回格式
      type: String,
      default: () => "data.data.data"
    },
    url: {
      type: String, // getGoodsListPopup
      default: "getGoodsListDropdown"
    }, // getGoodsListDropdown
    changeGoods: {
      type: Function,
      default: function() {
        // console.log("请写change方法");
      }
    },
    clear: {
      type: Function,
      default: function() {
        // console.log("请写change方法");
      }
    }
  },
  watch: {
    "scopes.row"(newVal) {
      if (this.updateArray.length > 0) {
        if (this.updateArray.includes(this.keyObj.label)) {
          this.query = newVal[this.keyObj.label];
        }
      } else {
        this.query = newVal[this.keyObj.label];
      }
    },
    query(newVal) {
      if (this.isFuzzy) {
        this.scopes.row[this.keyObj.label] = newVal;
      }
    }
  },
  data() {
    return {
      goodslist: [],
      query: "",
      page: 1,
      isLoading: false, // 是否加载中
      isFinish: false // 是否滚动到底
    };
  },
  created() {
    this.query = this.scopes.row[this.keyObj.label];
  },
  methods: {
    // 清除失去焦点
    clearGoods() {
      this.updateRow(this.scopes, {});
      this.clear();
      var dom = this.$refs.autoinput.$el.getElementsByClassName(
        "el-input__inner"
      )[0];
      dom && dom.blur();
    },
    // 改变row
    updateRow(scope, value) {
      if (this.updateArray.length === 0) {
        this.$set(scope.row, this.keyObj.label, value.goods_name);
        this.$set(scope.row, this.keyObj.value, value.goods_id);
      } else {
        this.updateArray.forEach(val => {
          this.$set(scope.row, val, value[val]);
        });
      }
      this.changeGoods && this.changeGoods(value, scope);
    },
    // 移除滚动加载
    removeScroll() {
      let box = document.getElementsByClassName(
        "el-autocomplete-suggestion__wrap"
      );
      for (let i = 0; i < box.length; i++) {
        box[i].onscroll = null;
      }
    },
    // 滚动加载
    scroll() {
      let box = document.getElementsByClassName(
        "el-autocomplete-suggestion__wrap"
      );
      // var a = document.querySelectorAll(".el-autocomplete-suggestion");
      // var b = document.querySelector(".el-autocomplete-suggestion .el-scrollbar")
      // a = a[1] ? a[1] : a[0];
      // console.log(a,b)
      // b.setAttribute(
      //   "style",
      //   "width: " + Object.keys(this.showObj).length * 300 + "px !important"
      // );
      // a.setAttribute(
      //   "style",
      //   "width: " + Object.keys(this.showObj).length * 300 + "px !important"
      // );
      var that = this;
      for (let i = 0; i < box.length; i++) {
        box[i].onscroll = function() {
          if (
            box[i].scrollTop + box[i].clientHeight + 200 >
              box[i].children[0].clientHeight &&
            !that.isLoading &&
            !that.isFinish
          ) {
            that.page++;
            that.getList(that.query);
            that.goodslist.push({
              loading: true
            });
            that.isLoading = true;
          }
        };
      }
    },
    remoteGoods: Debounce(function(query, callback) {
      if (this.beforeGetList() === false) {
        // 是否可以请求接口
        this.isFinish = true;
        callback && callback([]);
        this.isLoading = false;
        this.clearGoods();
        return;
      }
      // this.query = query;
      this.page = 1;
      this.isFinish = false;
      this.goodslist = [
        {
          goods_id: "编号",
          goods_name: "商品名称",
          disabled: true
        }
      ];
      this.getList(query, callback);
      this.scroll();
    }, 500),
    getList(query, callback) {
      let params = this.$publicFution.deepCopy(this.vxeParams);
      params.goods_name = query;
      params.page = this.page;
      params.per_page = 30;
      var ajaxFunc = this.$axMethod[this.url]
        ? this.$axMethod[this.url]
        : this.$api[this.url];
      ajaxFunc(this.$publicFution.deepCopy(params))
        .then(res => {
          if (this.isLoading) {
            this.goodslist.pop(); // loading需要删除一行
          }
          if (res.data.code == 200) {
            var resultBox = this.$publicFution.deepCopy(res);
            this.returnString.split(".").forEach(val => {
              // 不同的返回数据格式做不同的处理
              resultBox = resultBox[val];
            });
            if (!Array.isArray(resultBox)) {
              resultBox = [];
            }
            this.goodslist.push(...resultBox);
            if (res.data.data.last_page === res.data.data.current_page) {
              this.isFinish = true;
              this.goodslist.push({
                isFinish: true
              });
            }
            callback && callback(this.goodslist);
          } else {
            callback && callback([]);
          }
          this.isLoading = false;
        })
        .catch(() => {
          this.isFinish = true;
          callback && callback([]);
          this.isLoading = false;
        });
    },
    change(scope, value) {
      if (value.disabled) {
        return;
      }
      this.query = value.goods_name;
      this.updateRow(scope, value); // 默认改变项
      if (scope.$table) {
        scope.$table.updateStatus(scope);
      }
    }
  }
};
</script>

<style lang="less">
.goodsListContainer {
  .el-scrollbar {
    .el-autocomplete-suggestion__wrap {
      max-width: 1000px;
      overflow-x: scroll;
      .el-scrollbar__view {
        display: inline-block;
      }
    }
    .el-autocomplete-suggestion__wrap::-webkit-scrollbar {
      height: 26px;
      width: 26px;
    }

    &:hover {
      .el-autocomplete-suggestion__wrap::-webkit-scrollbar-thumb {
        height: 30px;
        // border-radius: 5px;
        background-color: rgba(168, 168, 168, 0.5);
      }
    }
    .el-autocomplete-suggestion__wrap::-webkit-scrollbar-thumb:hover {
      height: 30px;
      // border-radius: 5px;
      background-color: rgba(168, 168, 168, 0.5);
    }
  }
  // .goodsListTitle {
  //   // background-color: white;
  //   // height: 30px;
  //   // padding-left: 20px;
  //   // position: absolute;
  //   // left: 0px;
  //   // top: 0;
  // }
  .goodsListItem {
    li {
      display: inline-block;
      overflow: hidden;
      line-height: 34px;
      width: 80px;
      height: 34px;
    }
    & li:nth-child(1) {
      width: 70px;
    }

    & li:nth-child(2) {
      width: 320px;
    }

    & li:nth-child(3) {
      width: 90px;
      text-align: right;
    }

    & li:nth-child(4) {
      width: 90px;
      text-align: right;
    }
  }
}
</style>