export const paramsModule = {
  company_id: '',
  goods_id: '',
  goods_name: '',
  goods_type_id: "",
  customer_id: 0,
  specification_type: '', // 规格型号
  pattern: '',
  order_id: '',
  order_type: '', // 订单类型
  brand_id: '',
  factory_code: '',
  warehouse_id: '',
  is_stocks: 0, // 在库是否大于0：【0|否,1|是】
  is_available_stocks: 0, // 可用是否大于0：【0|否,1|是】
  bill_type: '',
  type: '',
  page: '',
  per_page: ''
}
export const goodsList = { // 需要传进来一个this对象
  data() {
    return {
      params: this.$publicFution.deepCopy(paramsModule),
      paramsBox: ['goods_name', 'brand_id', 'shopType', 'warehouse', 'specification_type', 'pattern', 'is_stocks', 'is_available_stocks'],// 参数数组
      tableColumnTop: [{
        type: "checkbox",
        width: 50,
      },
      {
        title: '数量',
        field: 'goods_num',
        headerAlign: "right", // 表头对齐方式
        align: "right", // 列对齐方式
        width: 70,
        editRender: {
          name: 'number',
          events: {
            input: ({
              row
            }) => {
              this.updateTableData([row], this.tableData)
            }
          }
        }
      },
      {
        title: '商品编号',
        field: 'goods_id',
        width: 100
      },
      {
        title: '商品名称',
        field: 'goods_name',
        minWidth: '250',
        slots: {
          default: ({ row, column }, h) => {
            return [
              <pre>{row[column.property]} </pre>
            ]
          }
        }
      },
      {
        title: '年周/批次',
        field: 'batch',
        width: 100,
        sortable: true
      },
      {
        title: '仓库名称',
        field: 'warehouse_name',
        width: 120,
        sortable: true
      },
      {
        title: '可用库存',
        field: 'available_stocks',
        headerAlign: "right",
        align: "right",
        width: 120,
        sortable: true
      }, {
        title: '在库库存',
        field: 'stocks',
        headerAlign: "right",
        align: "right",
        width: 120,
        sortable: true
      }, {
        title: '结存成本价',
        field: 'cost_price',
        headerAlign: "right",
        align: "right",
        width: 100,
        sortable: true
      }, {
        title: '售价',
        field: 'unit_price',
        headerAlign: "right",
        align: "right",
        width: 100,
        sortable: true
      }, {
        title: '品牌',
        field: 'brand_name',
        width: 120,
        sortable: true
      }, {
        title: '货位',
        field: 'allocation_name',
        width: 100
      },
      {
        title: '工厂代码',
        field: 'factory_code',
        width: 100
      },
      {
        title: '速记码',
        field: 'shorthand_code',
        width: 100
      },
      {
        title: '助记码',
        field: 'logogram',
        width: 100
      },
      {
        title: '规格型号',
        field: 'specification_type',
        width: 100
      }, {
        title: '花纹',
        field: 'pattern',
        width: 100
      },
      {
        title: '单位',
        field: 'unit_name',
        width: 80
      }
      ],
      tableColumnBottom: [],
      baseColumn: ['goods_id', 'goods_name']
    }
  },
  methods: {
    // 处理请求回来的数据
    dealData(result) {
      if (result.data.code === 200) {
        this.tableDataTop = result.data.data.data
        this.updateTableData(this.tableData, this.tableDataTop)
        this.$refs.xTable._proxy("setActiveCell", this.tableDataTop[0], 'goods_num')
      }
    },
    // 回显数据
    updateTableData(box, data) {
      box.forEach((val) => {
        data.forEach((twoVal) => {
          if (twoVal[this.rowId] === val[this.rowId]) {
            twoVal.goods_num = val.goods_num
          }
        })
      })
    },
    // 初始化配置
    initTableColumn() {
      // 配置不同 传配置
      this.tableColumnTop = this.tableColumnTop.map(val => {
        for (var i = 0; i < this.tableColumnConfig.length; i++) {
          if (val.field === this.tableColumnConfig[i].old_field) {
            return Object.assign(val, this.tableColumnConfig[i])
          }
        }
        return val
      })
      // 上面表格 跟下面表格用一个配置 就是数量不同
      var box = this.tableColumnTop.concat([])
      box[1] = {
        title: '数量',
        field: 'goods_num',
        width: 70,
        editRender: {
          name: 'number',
          events: {
            input: ({
              row
            }) => {
              this.updateTableData([row], this.tableDataTop)
            }
          }
        }
      }
      this.tableColumnBottom = box
      if (this.incloudTop.length > 0) {
        this.incloudTop = this.incloudTop.concat(this.baseColumn)
        this.tableColumnTop = this.tableColumnTop.filter(val => { // 筛掉上面表格的列
          for (var i = 0; i < this.incloudTop.length; i++) {
            if (this.incloudTop[i] === val.field || val.type === 'checkbox') {
              return val
            }
          }
          return false
        })
      } else {
        this.tableColumnTop = this.tableColumnTop.filter(val => { // 筛掉上面表格的列
          for (var i = 0; i < this.ExcludeTop.length; i++) {
            if (this.ExcludeTop[i] === val.field) {
              return false
            }
          }
          return val
        })
      }
      var bottom = []
      if (this.incloudTop.length > 0) {
        if (this.incloudBottom.length > 0) {
          bottom = this.incloudBottom.concat(this.baseColumn)
        } else {
          bottom = this.incloudTop.concat(this.baseColumn)
        }
        this.tableColumnBottom = this.tableColumnBottom.filter(val => { // 筛掉上面表格的列
          for (var i = 0; i < bottom.length; i++) {
            if (bottom[i] === val.field || val.type === 'checkbox') {
              return val
            }
          }
          return false
        })
      } else {
        if (this.ExcludeBottom.length > 0) {
          bottom = this.ExcludeBottom.concat([])
        } else {
          bottom = this.ExcludeTop.concat([])
        }
        this.tableColumnBottom = this.tableColumnBottom.filter(val => { // 筛掉下面表格的列
          for (var i = 0; i < bottom.length; i++) {
            if (bottom[i] === val.field) {
              return false
            }
          }
          return val
        })
      }
      this.paramsBox = this.paramsBox.filter(val => {
        for (var i = 0; i < this.ExcloudParams.length; i++) {
          if (this.ExcloudParams[i] === val) {
            return false
          }
        }
        return val
      })
    }
  },
  created() {
    this.initTableColumn()
    this.warehouseList = [];
    this.warehouseList.push({
      warehouse_id: this.searchParams.warehouse_id,
      warehouse_name: this.searchParams.warehouse_name
    });
    this.searchParams = this.$publicFution.filter(paramsModule, this.searchParams)
    this.params = this.$publicFution.deepCopy(this.searchParams)
    console.log(this.searchParams)
  },
  mounted() {
    this.onSearch();
  },
}
