const getDepartmentListMethod = (vm, parameter, list = 'departmentList') => {
  //部门附加模糊搜索
  vm.$set(vm, list, []);
  let params = {
    company_id: vm.params.company_id ? vm.params.company_id : "",
    department_name: vm.params.department_name,
    ...parameter
  };
  vm.$axMethod.getDepartmentListOpen(params).then(({
    data
  }) => {
    if (data.code == 200) {
      let result = data.data;
      vm.$set(vm, list, result);
    }
  });
};

