const getbrandListMethod = (vm, parameter, empty, callBack) => {
  //品牌下拉
  let params = {
    brand_name: parameter
  };
  vm.$set(vm, "brandList", []);
  vm.loading = true
  vm.$axMethod.getbrandListOpen(params).then(res => {
    let result = res.data.data;
    if (empty) {
      result.unshift({
        brand_id: -1,
        brand_name: '无品牌'
      })
    }
    callBack && callBack(result)
    vm.loading = false
    vm.$set(vm, "brandList", result);
  });
};

