const getBatchOptionMethod = (vm, parameter, f, bill_type, callBack) => {
  vm.$set(vm, "batchList", []);
  let params = {
    company_id: vm.params.company_id,
    warehouse_id: vm.params.warehouse_id,
    customer_id: vm.params.customer_id,
    goods_id: parameter.goods_id,
    bill_type: bill_type, //1对应的销售订单2对应的销售出库
    order_id: f ? f : "",
    type: 1
  };
  vm.loading = true
  vm.$axMethod.getBatchlist(params).then(({
    data
  }) => {
    let result = data.data;
    vm.$set(vm, "batchList", result);
    vm.loading = false
    callBack && callBack(data)
  });
};
