export default {
  // 初始化代理请求数据
  methods: {
    initProxy() {
      // 分页基本配置
      var paper = {
        "page-sizes": [30, 50, 100, 500], // 页数数组切换
        background: false, // 背景色
        "current-page": 1, // 当前页
        "pager-count": 7, // 显示的页码数量
        pageSize: 30 // 每页大小
      };
      // 代理基本配置配置
      if (this.ajax && this.ajax.url) {
        // 有分页配置分页内容
        if (this.isPage) {
          this.pager_Config = Object.assign(paper, this.pagerConfig);
        }
        var isProxy = this.ajax.Proxy !== undefined ? this.ajax.Proxy : true; // 默认开启代理
        var proxy = {
          sort: this.ajax.sort === undefined ? true : this.ajax.sort,// 服务端排序 默认为true
          props: {
            result: this.ajax.result ? this.ajax.result : "data.data.data",
            list: this.ajax.result ? this.ajax.result : "data.data.data",
            total: this.ajax.total ? this.ajax.total : "data.data.total"
          },
          ajax: {
            query: ({
              $grid,
              page,
              sort
            }, index, pageSize) => {
              var params = {};
              if (!this.ajax.params) {
                this.ajax.params = {};
              } else {
                params = this.$publicFution.deepCopy(this.ajax.params);
              }
              if (this.isPage) {
                if (pageSize) {
                  $grid.tablePage.pageSize = pageSize;
                }
                $grid.tablePage.currentPage = isNaN(index) ? page.currentPage : index;
                params.page = page.currentPage;
                params.per_page = page.pageSize;
                if (this.$parent.goPage) this.$parent.goPage = page.currentPage;
              }
              if (sort && sort.field) {// 服务端排序
                params.order_key = sort.field
                params.order_by = sort.order
              }
              params.hideLoding = true;
              return new Promise((resolve) => {
                this.$axios({
                  method: "get",
                  url: this.$api[this.ajax.url] ? this.$api[this.ajax.url] : this.ajax.url,
                  params: params
                }).then(result => {
                  if (result.code != 200) {
                    resolve(result);
                    return
                  }
                  if (isProxy) {
                    resolve(result);
                  }
                  this.$emit("getData", {
                    // 不用代理需要手动把数据 resolve(result);
                    data: result.data,
                    next: resolve
                  });
                  var str = this.ajax.result ? this.ajax.result : "data.data.data"
                  str.split('.').forEach(val => {
                    result = result[val]
                  })
                  if (Array.isArray(result)) {
                    this.$parent.tableData = result;
                  } else {
                    this.$parent.tableData = []
                  }
                  setTimeout(() => {
                    this._updateFooter();
                  }, 50);
                  // if (result && result.length === 0) {// 是否展示合计行
                  //   this.showFooter = false
                  //   if (this.$refs.vxeGrid) {
                  //     var table = this.$refs.vxeGrid.$el
                  //     if (table) {
                  //       var right = table.getElementsByClassName('vxe-table--fixed-right-wrapper')[0]
                  //       var left = table.getElementsByClassName('vxe-table--fixed-left-wrapper')[0]
                  //       setTimeout(() => {
                  //         left && (left.style.height = '90px')
                  //         right && (right.style.height = '90px')
                  //       }, 50)
                  //     }
                  //   }
                  // } else {
                  //   this.showFooter = true
                  // }
                });
              });
            }
          }
        };
        this.proxy_Config = Object.assign(proxy, this.proxyConfig);
      }
    }
  },

}