<template>
  <!-- 付款方式 -->
  <el-select v-model="value" placeholder="请选择" :disabled='disabled' clearable @clear='change("")' filterable @focus="getList" @change="change">
    <el-option v-for="item in list" :key="item.payment_method_id" :label="item.payment_method_name" :value="item.payment_method_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "payMethod",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    value: null,
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "payment_method_name"
    },
    modelValue: {
      type: String,
      default: "payment_method_id"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value !== undefined &&
        this.value !== null &&
        this.value !== ""
      ) {
        if (!this.list.some(val => val[this.modelValue] === this.value)) {
          this.list.push({
            payment_method_id: this.value,
            payment_method_name: this.fatherParams[this.modelLabel]
          });
        }
      } else if (this.value === 0 && !this.fatherParams[this.modelLabel]) {
        // 解决为0的情况
        this.value = "";
      }
    },
    getList() {
      this.$axios({
        url: "/purchase_center/Open/PaymentMethod/getPaymentMethodList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.payment_method_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

