import filters from "common/filters/index.js";

export default {
  methods: {
    // 初始化行 可再此 封装简化每个列的配置
    initColumn(tableColumn) {
      // 每一列基本配置
      var column = {
        showOverflow: "tooltip", // ellipsis省略号, title原生title，tooltip  继承 table 的 show-header-overflow
        showHeaderOverflow: "tooltip", // ellipsis省略号, title原生title，tooltip  继承 table 的 show-header-overflow
        headerAlign: "left", // 表头对齐方式
        align: "left" // 列对齐方式
      };
      for (var i = 0; i < tableColumn.length; i++) {
        tableColumn[i] = Object.assign({}, column, tableColumn[i]);
        if (typeof tableColumn[i].formatter === "string") {
          tableColumn[i].formatter = (function (key) {
            return function ({ row, column }) {
              return filters[key](row[column.property]);
            };
          })(tableColumn[i].formatter);
        }
        if (tableColumn[i].title === '操作') {
          tableColumn[i].showOverflow = false
          tableColumn[i].headerAlign = tableColumn[i].align == 'center' ? 'center' : 'right'
          tableColumn[i].align = tableColumn[i].align == 'center' ? 'center' : 'right'
        }
        // 序号处理
        if (
          tableColumn[i].type === "index" ||
          tableColumn[i].type === "seq"
        ) {
          tableColumn[i].headerAlign = "center";
          tableColumn[i].width = "50";
          tableColumn[i].align = "center";
        }
        // 多选框
        if (tableColumn[i].type === "checkbox") {
          tableColumn[i].headerAlign = "center";
          tableColumn[i].width = "44";
          tableColumn[i].align = "center";
        }
        // 加上头部icon
        if (tableColumn[i].headIcon) {
          if (!tableColumn[i].slots) {
            tableColumn[i].slots = {};
          }
          tableColumn[i].slots.header = {};
          tableColumn[i].slots.header = (function (icon) {
            return ({ row, rowIndex, column }, h) => {
              var a;
              if (typeof icon === "string") {
                a = (
                  <span>
                    <i class={icon}> </i>
                    {column.title}
                  </span>
                );
              } else {
                a = <span class="headIcon">{column.title}</span>;
              }
              return [a];
            };
          })(tableColumn[i].headIcon);
        }
        // pre处理
        if (
          !tableColumn[i].slots &&
          !tableColumn[i].formatter &&
          !tableColumn[i].editRender &&
          tableColumn[i].field === "goods_name"
        ) {
          tableColumn[i].slots = {
            default: ({ row, column }, h) => {
              return [<pre>{row[column.property]} </pre>];
            }
          };
        }
        // 处理状态颜色变化
        // if (
        //   !tableColumn[i].slots &&
        //   !tableColumn[i].editRender &&
        //   (tableColumn[i].field === "auditor_status_name" ||
        //     tableColumn[i].field === "auditor_status")
        // ) {
        //   tableColumn[i].slots = {
        //     default: ({ row, column }, h) => {
        //       var className;
        //       if (
        //         row[column.property] === "未审核" ||
        //         row[column.property] === "待审核" ||
        //         row[column.property] === 1
        //       ) {
        //         className = "primary";
        //       } else if (
        //         row[column.property] === "已审核" ||
        //         row[column.property] === 2
        //       ) {
        //         className = "success";
        //       } else if (
        //         row[column.property] === "作废" ||
        //         row[column.property] === "关闭" ||
        //         row[column.property] === 3 ||
        //         row[column.property] === 4
        //       ) {
        //         className = "warning";
        //       }
        //       return [
        //         <span class={className}>
        //           {column.property === "auditor_status"
        //             ? filters.statusFormat(row[column.property])
        //             : row[column.property]}
        //         </span>
        //       ];
        //     }
        //   };
        // }
      }

      // 记忆自定义表头顺序--start
      const localConfig = JSON.parse(window.localStorage.getItem(`vtable-${this.rowId}-${this.$route.fullPath}`) || '[]')
      if (localConfig.length) {
        const newConfigArr = []
        localConfig.forEach(item => {
          const res = tableColumn.find(child => child.title === item.title) || {} // checkbox无title(undefined), 默认位置不变
          newConfigArr.push(res)
        })
        tableColumn = newConfigArr
      }
      // 记忆自定义表头顺序--end

      if (this.$refs.vxeGrid) {
        this._proxy("reloadColumn", tableColumn);
      } else {
        this.vxeTableColumn = tableColumn.concat()
      }
    },
    // 获取远端字段权限
    getFieldPower() {
      this.$axios({
        url: 'v2/open/FieldAuth/getFieldList',
        method: 'get',
        params: {
          menu_action: this.fieldUrl
        }
      }).then(({ data }) => {
        if (data.code) {
          var box = this.tableColumn.filter(val => {
            for (var i = 0; i < data.data.length; i++) {
              if (data.data[i].field === val.field || val.type === 'checkbox' || val.type === 'index' || val.type === 'sqp' || val.title === '操作') {
                return true
              }
            }
          })
          this.initColumn(box); // 初始化行数据
          this.initAttrs();
        }
      }).catch(() => {
        this.initColumn(this.tableColumn); // 初始化行数据
        this.initAttrs();
      })
    }
  }
}