<template>
  <!-- 商品类型 -->
  <div>
    <el-cascader :clearable="clearable" v-model="list" ref="cascader" :disabled='disabled' @change="handleChange" :props="props"></el-cascader>
  </div>
</template>

<script>
export default {
  name: "goodsType",
  data() {
    const _that = this;
    return {
      clearable: true,
      list: [],
      props: {
        lazy: true,
        lazyLoad(node, resolve) {
          const { value, level } = node;
          _that.$axMethod.getselectType({ p_id: value }).then((res) => {
            if (res.data.code == 200) {
              let Items = res.data.data;
              const nodes = Items.map((value, i) => {
                return {
                  value: value.goods_type_id,
                  label: value.goods_type_name,
                  leaf: level >= 2,
                };
              });
              resolve(nodes);
            }
          });
        },
      },
    };
  },
  watch: {
    value(newVal) {
      if (Array.isArray(newVal)) {
        // 监听父组件v-model 变化 如果为数组为回显，如果为false就是清空
        this.list = newVal;
      }
      if (!newVal) {
        this.clear();
      }
    },
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {
      default: [],
    },
  },
  methods: {
    handleChange(val) {
      this.$emit("input", val); // 父组件只要最后一级id
      this.$emit("change", val);
    },
    clear() {
      if (this.$refs.cascader.clearCheckedNodes) {
        this.$refs.cascader.clearCheckedNodes();
      } else {
        this.value = [];
      }
    },
  },
};
</script>

