import './public-path';
import Vue from 'vue'
import App from './App.vue'
import Router from './router'
import store from './store'
import mixin from '@/utils/mixin.js'
import api from "@/sever/api";
import install from 'common'
Vue.mixin(mixin)
Vue.config.silent = true
Vue.prototype.$api = api;
Vue.use(install)
import rules from "./utils/rules"; //全局定义表单验证规则
Vue.prototype.rules = rules;
let contexts = require.context('./components/groupPlugin/public', true, /\.vue$/)
contexts.keys().forEach(component => {
  let componentEntity = contexts(component).default
  // 使用内置的组件名称 进行全局组件注册
  Vue.component(componentEntity.name, componentEntity)
})
Vue.config.productionTip = false
let instance = null
const render = (props = {}) => {
  const { container, routerBase } = props;
  const router = Router.getRouter(window.__POWERED_BY_QIANKUN__ ? routerBase : process.env.BASE_URL)
  Vue.prototype.$mainProps = props
  Vue.prototype.$myDocument = container
  instance = new Vue({
    router,
    store,
    render: (h) => h(App)
  }).$mount(container ? container.querySelector('#app') : '#app')
}
if (!window.__POWERED_BY_QIANKUN__) {//独立运行
  render()
}
// 生命周期
export async function mount(props) {
  console.log(props, 'props')
  // 从生命周期 mount 中获取通信方法，props默认会有onGlobalStateChange和setGlobalState两个api
  render(props);
}
/**
 * bootstrap 只会在微应用初始化的时候调用一次，下次微应用重新进入时会直接调用 mount 钩子，不会再重复触发 bootstrap。
 * 通常我们可以在这里做一些全局变量的初始化，比如不会在 unmount 阶段被销毁的应用级别的缓存等。
 */
export async function bootstrap() {
  // console.log('react app bootstraped');
}
/**
   * 应用每次 切出/卸载 会调用的方法，通常在这里我们会卸载微应用的应用实例
   */
