
import axios from "axios"
import Qs from "qs";
import {
  showFullScreenLoading,
  tryHideFullScreenLoading
} from './loading'
import {
  serverMoudle,
  Logout
} from './fetch'
import {Message} from 'element-ui'
// axios 配置
let repeatRequestCollection = []; //存储每个请求的取消函数和axios标识
let cancelToken = axios.CancelToken;
let removePending = (config) => {
  for (let i in repeatRequestCollection) {
    if (repeatRequestCollection[i].url === config.url) { //在当前请求在数组中存在时执行取消函数
      repeatRequestCollection[i].f("cancelRequest"); //执行取消操作
    }
  }
}
const service = axios.create({
  baseURL: 'https://test-www.taijierp.com', // url = base url + request url
  headers: {
    "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
  },
  transformRequest: [
    function (data) {
      data = Qs.stringify(data)
      return data
    }
  ]
})
// 拦截器
service.interceptors.request.use(
  config => {
    let flag;
    let configArry = config.url ? config.url.split('/') : ['open'];
    flag = configArry.some(function (configArry) {
      return ['Publics', 'open', 'publics', 'Open'].includes(configArry);
    })
    if (!flag) {
      if (config.params && config.params.hideLoding) {
        tryHideFullScreenLoading()
      } else {
        showFullScreenLoading()
      }
    }
    if (config.params && config.params.hideLoding) {
      delete config.params.hideLoding
    }
    if (config.method == "post") {
      config.data = {
        ...config.data,
        sign: serverMoudle(config.data),
      }
      removePending(config);
      config.cancelToken = new cancelToken(function executor(c) {//本次axios请求的配置添加cancelToken
        repeatRequestCollection.push({
          url: config.url,
          f: c
        });
        //将本次的url添加到repeatRequestCollection中，因此对于某个url第一次发起的请求不会被取消，因为还没有配置取消函数
      });
      // 下边是添加sign
    } else if (config.method == "get") {

      if (config.params && config.params.repeat) {
        delete config.params.repeat
      } else {
        removePending(config);
        config.cancelToken = new cancelToken(function executor(c) {//本次axios请求的配置添加cancelToken
          repeatRequestCollection.push({
            url: config.url,
            f: c
          });
          //将本次的url添加到repeatRequestCollection中，因此对于某个url第一次发起的请求不会被取消，因为还没有配置取消函数
        });
      }
      config.params = {
        ...config.params,
        sign: serverMoudle(config.params),
      };
    }

    const getToken = window.localStorage.getItem("token")
    if (getToken) {
      config.headers.Token = getToken;
    }
    // 每次请求添加sign
    return config;
  },
  err => {
    tryHideFullScreenLoading();
    Message({
      showClose: true,
      message: err.msg,
      type: 'error'
    })
    return Promise.reject(err);
  }
);
// http response 拦截器
service.interceptors.response.use(
  response => {
    if (response.config.method == "post") {
      removePending(response.config); //在一个axios响应后再执行一下取消操作，把已经完成的请求从repeatRequestCollection中移除
    }
    tryHideFullScreenLoading()
    let messageCodeArr = [400, 402, 403] // 同样得功能 message 提示
    const data = response.data;
    if (data.code == 401) {
        Logout()
        history.replaceState(null, '', '/login') 
    } else if (data.code == 503) {
      Message.error(data.msg)
      Logout()
      history.replaceState(null, '', '/login') 
    }
    if (messageCodeArr.indexOf(data.code) > -1) {
      Message.closeAll() // 防止一次出现多个弹框
      data.msg && Message.error(data.msg)
    }
    return response;
  },
  error => {
    tryHideFullScreenLoading()
    if (error.message == "cancelRequest") {
      Message.closeAll()
      return {
        data: {
          code: 404
        }
      }
    } else {
      Message({
        message: "网络错误",
        type: "error",
        duration: 5 * 1000
      });
    }
    return error
  }
);
export default service;
