<template>
  <div>
    <ul>
      <li v-for="itme in imgArr" :key="itme" class="imgClass">
        <img :src="itme.url" :alt="itme.name" class="img">
      </li>
    </ul>
    <input type="file" name="upload" ref="fileInt" multiple @change="changeHander">
  </div>
</template>

<script>
export default {
  name: "jybdupload",
  data() {
    return {
      imgArr: []
    };
  },
  methods: {
    changeHander(val) {
      console.log(val);
      let arry = Array.from(val["target"].files);
      if (arry.length) {
        arry.forEach(itme => {
          let format = itme.type.split("/")[0];
          if (format == "image") {
            let url = URL.createObjectURL(itme);
            this.imgArr.push({ url, name: itme.name });
          } else if (format == "") {
            this.imgArr.push({
              url:
                "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1763633455,1584227941&fm=26&gp=0.jpg",
              name: itme.name
            });
          }
        });
      } else {
        alert("上传失败");
      }
    }
  }
};
</script>

