export default {
  methods: {
    // 操作
    contextMenuClickEvent({
      menu,
      row,
      column
    }) {
      let xGrid = this.$refs.vxeGrid;
      switch (menu.code) {
        case "clear":
          xGrid.clearData(row, column.property);
          break;
        case "export":
          xGrid.exportCsv();
          break;
        case "reload":
          xGrid.commitProxy("query");
      }
    },
    initMenu() {
      var menu = {
        body: {
          options: [
            [{
              code: "copy",
              name: "复制内容",
              visible: true,
              disabled: false,
              prefixIcon: "fa fa-copy"
            }
              // {
              //   code: "clear",
              //   name: "清除内容",
              //   visible: true,
              //   disabled: false
              // },
              // {
              //   code: "reload",
              //   name: "刷新表格",
              //   visible: true,
              //   disabled: false
              // }
              // {
              //   code: "export",
              //   name: "导出.csv",
              //   visible: true,
              //   disabled: false,
              //   prefixIcon: "fa fa-download"
              // }
            ]
          ]
        }
      };
      // 常用配置
      this.context_menu = Object.assign(menu, this.menu);
    }
  },
}