<template>
  <!-- 采购申请环境 -->
  <el-select clearable v-model="value" @change="change" :disabled='disabled' maxlength="100" filterable placeholder="请选择">
    <el-option v-for="item in list" :label="item.label" :value="item.value" :key="item.value">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getSystem",
  data() {
    return {
      list: [
        // {
        //   label: '',
        //   value: ''
        // },
        {
          label: "scm环境",
          value: 1
        },
        {
          label: "gyl环境",
          value: 2
        }
      ]
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    modelLabel: {
      type: String,
      default: "from_name"
    },
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    modelValue: {
      type: String,
      default: "from_id"
    },
    value: null
  },
  watch: {
    value() {
      if (this.value === 0) {
        this.value = "";
      }
    }
  },
  methods: {
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item.value === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.label;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

