import Vue from 'vue'
import VueRouter from "vue-router";
// import '@/permission' // permission control
import setPermission from '@/permission.js'

Vue.use(VueRouter);

let routerList = []
function inporantall(r) {
  r.keys().forEach(
    (key) => {
      if (r(key).default) {
        routerList.push(r(key).default)
      }
    });
};
inporantall(require.context("./ServiceStation", true, /\.js/));

const Router = function(){
  this.routerList = routerList
}

// 获取路由表
Router.getRouterList = function(){
  return this.routerList
}

/**
 * getRouter 获取路由实例
 * @param {string} base 基址
 * @returns 路由实例
 */
Router.getRouter = (function (){
  let instance = null
  return function(base){
    if(!instance){
      instance = new VueRouter({
        base,
        mode: 'history',
        routes: routerList
      })
      // 添加路由权限控制
      setPermission(instance)
    }

    return instance
  }
})()

export default Router
