<template>
  <!-- 库存盘点批次 -->
  <div>
    <el-autocomplete v-model="query" v-if='type === 2' :fetch-suggestions="(queryString,callback) => {batchRemote(scope,queryString,callback)}" @blur="blurchange(scope)" @select="(value)=>{ autocompleteSelect(value)}" placeholder="请输入批次">
      <template slot-scope="{ item }">
        <div v-if="item.isEmpty && batchList.length < 2" style="textAlign: center;color:#dedede" @click.stop.prevent="">暂无数据</div>
        <div v-else>{{item.batch}}</div>
      </template>
    </el-autocomplete>
    <el-select v-model="query" v-if='type === 1' filterable @clear="clear(scope.row)" :loading="loading" @focus="batchRemote(scope)" clearable value-key="batch_id" @change="autocompleteSelect(query)" placeholder="请选择">
      <div class='batchBox' style='padding: 0 20px;'>
        <span v-for="(val, key) in showObj" :key='key' :class="'batch_' + key">
          {{ val }}
        </span>
      </div>
      <el-option v-for="item in batchList" :key="item.batch_id" :label="item.batch" :value="item">
        <div class='batchBox'>
          <span v-for="(val, key) in showObj" :key='key' :title='item[key]' :class="'batch_' + key">
            {{ item[key] }}
          </span>
        </div>
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      query: "",
      loading: false,
      batchList: [], // 批次数组
    };
  },
  watch: {
    query(newVal) {
      if (this.type === 2) {
        this.scope.row.batch = newVal;
      }
    },
  },
  props: {
    vxeParams: {
      // 请求批次参数
      type: Object,
      default: () => {},
    },
    type: {
      type: Number,
      default: 1, // 1 默认批次 2 是盘点批次
    },
    scope: {
      type: Object,
      default: () => {},
    },
    url: {
      type: String,
      default: "getBatchlist",
    },
    showObj: {
      // 批次显示行列
      type: Object,
      default: () => {
        return {
          batch: "年周/批次",
          production_time: "生产日期",
          warehouse_name: "仓库名称",
          allocation_name: "货位名称",
          available_stocks: "可用库存",
          stocks: "在库库存",
        };
      },
    },
    batchBlur: {
      type: Function,
      default: function () {
        console.log("请写blur方法");
      },
    },
    batchChange: {
      // 回调方法
      type: Function,
      default: function () {
        console.log("请写change方法");
      },
    },
  },
  created() {
    this.query = this.scope.row.batch ? this.scope.row.batch : "";
    if (this.type === 1) {
      this.batchRemote(this.scope);
    }
  },
  methods: {
    // 清除批次
    clear(row) {
      [
        "batch",
        "batch_id",
        "warehouse_name",
        "warehouse_id",
        "allocation_name",
        "allocation_id",
      ].forEach((val) => {
        this.$set(row, val, "");
      });
    },
    // 批次改变
    autocompleteSelect(value) {
      if (value.isEmpty === 1) {
        return;
      }
      this.query = value.batch;
      this.batchChange && this.batchChange(value, this.scope);
      if (this.scope.$table) {
        this.scope.$table.updateStatus(this.scope);
      }
    },
    blurchange(scope) {
      this.batchBlur(scope, this);
    },
    // 获取批次
    batchRemote(scope, queryString, callback) {
      this.loading = true;
      this.batchList = [];
      if (this.type === 2) {
        this.vxeParams.batch = queryString;
      }
      var resultAjax = this.$axMethod[this.url]
        ? this.$axMethod[this.url]
        : this.$api[this.url];
      resultAjax(this.vxeParams).then((res) => {
        if (res.data.code === 200) {
          this.batchList = res.data.data;
          if (this.type === 2) {
            this.batchList.push({ isEmpty: 1 });
          }
          callback && callback(this.batchList);
          this.loading = false;
        }
      });
    },
  },
};
</script>

<style lang='less' scoped>
/deep/ .el-loading-spinner {
  margin-top: -8px;
}
</style>


<style lang="less">
.batchBox {
  > span {
    display: inline-block;
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
    padding: 0px 5px;
    height: 34px;
  }
  .batch_warehouse_name {
    width: 250px;
  }
}
</style>