<template>
  <!-- 集采上传 -->
  <el-upload class="uploadFile" :headers='headers' :action="$axios.defaults.baseURL + url" ref='upload' @on-preview='lookBig' :before-upload='beforeUploadAction' :data='extendParams' :on-error='error' :on-success='onSuccess' :before-remove='beforeRemove' :on-preview="handlePreview" :on-remove="handleRemove" :file-list="list" list-type="picture">
    <slot slot="default"></slot>
    <slot slot="tip" name='tip'>
    </slot>
    <vxe-modal v-model="isShowImg" width='1200' title='图片预览' :esc-closable='true' :mask-closable='true' destroy-on-close height="800" min-width="500" min-height="100" resize remember storage>
      <div style='text-align:center'>
        <img :src="imgUrl" alt="">
      </div>
    </vxe-modal>
  </el-upload>
</template>

<script>
export default {
  name: 'uploadCommon',
  data() {
    return {
      list: [],
      imgUrl: "",
      headers: {
        token: localStorage.getItem("token")
      },
      isShowImg: false
    };
  },
  props: {
    value: {
      // 回显字段
      type: Array,
      default: function(arr) {
        return [];
      }
    },
    url: {
      type: String,
      default: "/v2/open/Upload/uploadImg"
    },
    beforeRemove: {
      type: Function,
      default: () => true
    },
    extendParams: {
      type: Object,
      default() {
        return {};
      }
    },
    beforeUpload: {
      // 外部拦截
      type: Function,
      default: () => {
        return true;
      }
    }
  },
  // watch: {
  //   value (newVal) {
  //     this._initList(newVal);
  //   }
  // },
  mounted() {
    this._initList(this.value);
  },
  methods: {
    // 初始化
    _initList(arr) {
      if (typeof arr === "string" && arr.length > 5) {
        arr = arr.split(",");
      }
      if (Array.isArray(arr)) {
        this.list = arr.map((val, key) => {
          return {
            url: val.url,
            name: val.name //"图片" + key
          };
        });
      }
      this.listChange(this.list);
    },
    // 上传失败
    error() {
      this.$message.error("上传失败,请稍后再试");
    },
    // // 上传
    // httpRequest (data) {
    //   var params = new FormData()
    //   params.append('filename',data.file)
    //   this.$axios({
    //     method: 'post',
    //     url: 'upload/UploadFile/addAllFile',
    //     data: params
    //   }).then((data) => {
    //     console.log(data)
    //   })
    // },
    isImg(str) {
      var result = str.substring(str.lastIndexOf(".") + 1);
      if (
        result == "jpg" ||
        result == "jpeg" ||
        result == "png" ||
        result == "bmp"
      ) {
        return true;
      }
      return false;
    },
    isPDF(str) {
      var result = str.substring(str.lastIndexOf(".") + 1);
      if (result == "pdf") {
        return true;
      }
      return false;
    },
    // 上传之前
    beforeUploadAction(file) {
      if (!this.beforeUpload()) {
        return false;
      }
      var pdfLength = 0,
        imgLength = 0,
        MB = 1024 * 1024;
      for (var i = 0; i < this.list.length; i++) {
        if (this.isPDF(this.list[i].name)) {
          pdfLength++;
        }
        if (this.isImg(this.list[i].name)) {
          imgLength++;
        }
      }
      if (
        (imgLength >= 6 && this.isImg(file.name)) ||
        (pdfLength >= 3 && this.isPDF(file.name))
      ) {
        this.$message.warning(
          "建议上传合同附件，如合同扫描件等；图片支持jpg、jpeg、png、bmp格式，大小不超过2MB，最多上传6张；文档附件不超过10MB，最多上传3份"
        );
        return false;
      }
      if (this.isImg(file.name)) {
        if (file.size > 2 * MB) {
          this.$message.error("图片大小不能超过2MB");
          return false;
        }
      } else if (this.isPDF(file.name)) {
        if (file.size > 10 * MB) {
          this.$message.error("pdf文件大小不能超过10MB");
          return false;
        }
      } else {
        this.$message.error("只能上传pdf和图片");
        return false;
      }
      return true;
    },
    onSuccess(response, file, fileList) {
      if (response.code === 200) {
        this.listChange(fileList);
      } else {
        fileList.pop();
        this.$message.error(response.msg);
      }
    },
    // 列表改变
    listChange(fileList) {
      this.list = fileList;
      this.$emit(
        "input",
        fileList
          .map(val => {
            return val.response ? val.response.data : val.name;
          })
          .join(",")
      );
      this.$emit("success", fileList);
    },
    handleRemove(file, fileList) {
      this.listChange(fileList);
      return this.beforeRemove(file, fileList);
    },
    handlePreview(file) {
      this.imgUrl = file.url;
      if (this.isPDF(file.name)) {
        window.open(this.imgUrl, "_blank");
      } else {
        this.isShowImg = true;
      }
    }
  }
};
</script>

<style lang='less' scoped>
.uploadFile {
  /deep/ .el-upload-list--picture {
    &::after {
      content: "";
      display: block;
      clear: both;
    }
    .el-upload-list__item {
      width: 250px;
      float: left;
    }
  }
}
</style>