<template>
  <!-- vxe表格二次封装 -->
  <div class='vxeTable'>
    <vxe-grid 
        border 
        columnKey
        resizable 
        ref='vxeGrid'
        auto-resize 
        show-overflow 
        highlight-hover-row 
        highlight-current-row 
        show-header-overflow 
        :size='size' 
        v-if='isShow' 
        :data="xData" 
        :row-id="rowId"
        :params='params' 
        :height='height'
        :stripe="stripe" 
        :row-style='rowStyle' 
        :toolbar="tool_Config" 
        :customs.sync="customs" 
        :max-height="maxHeight"
        :edit-rules="editRules"
        :show-footer='showFooter' 
        :span-method='spanMethod' 
        :columns="vxeTableColumn" 
        :edit-config="edit_Config" 
        :radio-config='radioConfig' 
        :cell-class-name='cellClassName' 
        :sort-config="{trigger: 'cell'}" 
        :mouse-config="mouse_Config" 
        :checkbox-config='select_Config' 
        :footerMethod='footerMethods' 
        :proxy-config="proxy_Config" 
        :pager-config="pager_Config" 
        :column-min-width="columnMinWidth"
        :row-class-name='($event) => { return rowClass($event)}' 
        :keyboard-config="{isArrow: true, isDel: true, isTab: true, isEdit:true, editMethod: isEdit }" 
        @cell-click='cellClick'
        @current-change="currentChangeEvent" 
        @cell-mouseenter='cellHover' 
        @select-all='selectChange' 
        @page-size-change='sizeChange' 
        @cell-context-menu="cellContextMenuEvent" 
        @select-change='selectChange' 
        @edit-actived='editActived' 
        @radio-change='radioChange' 
        @page-change='pageChange' 
        @cell-dblclick="cellDblclick" 
        @toolbar-button-click="toolbarButtonClick"
      >
      <template v-slot:empty>
        <div>
          {{ emptyText }}
        </div>
      </template>
    </vxe-grid>
  </div>
</template>

<script>
import tool from "./js/tool"; // 工具栏
import edit from "./js/edit"; // 可编辑配置 复选框配置
import event from "./js/event"; // 监听vxe组件的事件
import initColumn from "./js/initColumn"; // 初始化列
import methods from "./js/methods"; // 自己封装的方法
import mouse from "./js/mouse"; // 鼠标右击事件
import props from "./js/props"; // prop传进来的属性
import proxyData from "./js/proxyData"; // 数据代理相关
import dragable from "./js/dragable"; // dragable
export default {
  mixins: [
    edit,
    event,
    initColumn,
    methods,
    mouse,
    props,
    proxyData,
    tool,
    dragable,
  ],
  name: "xTable",
  data() {
    return {
      timer: null,
      params: {},
      loading: false,
      mouse_Config: {},
      vxeTableColumn: [],
      currentRdit: {}, // 当前操作的行
      hoverCurrentRow: false, // 鼠标悬浮的行
      currentRditColumn: [], //  当前操作的行的操作列数组
      showFooter: false,
      footerMethods: null, // 合计方法
      isShow: false,
      proxy_Config: null, // 代理配置（代理请求数据）
      pager_Config: null, // 页数配置
      tool_Config: null, // 工具配置
      edit_Config: {}, // 编辑配置
      context_menu: {}, // 快捷菜单
      select_Config: {}, // 复选框配置
      radioConfig: {
        trigger: "row",
      }, //单选框配置
    };
  },
  created() {
    this.initEdit(); //初始化编辑配置
    this.initProxy(); // 初始化代理请求数据
    this.initFooter(); // 初始化合计
    this.initMenu(); // 初始化鼠标右击事件
    this.initTool(); // 初始化工具栏
    if (this.fieldUrl) {
      this.getFieldPower(this.tableColumn); // 初始化行数据
    } else {
      this.initColumn(this.tableColumn); // 初始化行数据
      this.isShow = true;
    }
  },
  methods: {
    initAttrs() {},
  },
  mounted() {
    if (this.dragable) {
      setTimeout(() => {
        this.initDrag();
      }, 2000);
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  },
};
</script>

