<template>
  <!-- 货位渲染器 -->
  <div v-loading='loading' element-loading-spinner="el-icon-loading">
    <el-select v-model="scope.row[modelKey]" placeholder="请选择" remote :remote-method="getAllocationList" popper-class='allocationClass' filterable @focus="getAllocationList('')" @change="allocation_Change" clearable>
      <ul class="warehouse-ul warehouse-ul2 warehouse-ul3">
        <li v-for="(val, key) in showObj" :key='key' v-if='val'>
          {{ val }}
        </li>
      </ul>
      <el-option v-for="(item,key) in allocationList" :key="key" :label="item.allocation_name" :value='item.allocation_id'>
        <div v-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          <i class='icon el-icon-loading'></i> 拼命加载中
        </div>
        <div v-else-if='item.isFinish' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          已经到底了
        </div>
        <ul class="warehouse-ul  warehouse-ul3">
          <li v-for="(val, key) in showObj" :key='key'>
            {{ item[key] }}
          </li>
        </ul>
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      allocationList: [], // 货位数组
      loading: false,
      isLoading: false, // 是否加载中
      isFinish: false, // 是否滚动到底
      page: 1,
      query: "",
    };
  },
  props: {
    modelKey: {
      type: String,
      default: "allocation_id",
    },
    modelLabel: {
      type: String,
      default: "allocation_name",
    },
    vxeParams: {
      type: Object,
      default: () => null,
    },
    scope: {
      type: Object,
      default: () => {},
    },
    showObj: {
      type: Object,
      default: () => {
        return {
          allocation_name: "货位名称",
          stocks: "在库库存",
        };
      },
    },
    isPage: {
      // 是否开启分页
      type: Boolean,
      default: true,
    },
    url: {
      type: String,
      default: "stockTakingGetGoodsList",
    },
    allocationChange: {
      type: Function,
      default: function () {
        console.log("请写change方法");
      },
    },
  },
  created() {
    var box = this.$publicFution.deepCopy(this.scope.row);
    box.allocation_id = box[this.modelKey];
    box.allocation_name = box[this.modelLabel];
    if (box.allocation_id === 0 && !box.allocation_name) {
      // id是零并且没有名字  不回显
      this.scope.row[this.modelKey] = "";
      return;
    }
    this.allocationList.push(box);
  },
  methods: {
    // 货位改变
    allocation_Change(value) {
      var result = this.allocationList.filter(
        (obj) => value === obj.allocation_id
      )[0];
      try {
        this.scope.row[this.scope.column.property] = result.allocation_name;
      } catch (error) {}
      this.allocationChange &&
        this.allocationChange(
          this.scope,
          result ? result : {},
          this.allocationList
        );
      if (this.scope.$table) {
        this.scope.$table.updateStatus(this.scope);
      }
    },
    // 滚动加载
    scroll() {
      let box = document.getElementsByClassName("allocationClass");
      let currentSelect,
        that = this;
      for (var i = 0; i < box.length; i++) {
        if (box[i].style.display !== "none") {
          currentSelect = box[i];
        }
      }
      if (currentSelect) {
        currentSelect = currentSelect.getElementsByClassName(
          "el-select-dropdown__wrap"
        )[0];
        currentSelect.onscroll = function () {
          if (
            currentSelect.scrollTop + currentSelect.clientHeight + 200 >
              currentSelect.children[0].clientHeight &&
            !that.isLoading &&
            !that.isFinish
          ) {
            that.page++;
            that.allocationList.push({
              loading: true,
            });
            that.getList();
            that.isLoading = true;
          }
        };
      }
    },
    getAllocationList(query) {
      this.query = query;
      if (this.isPage === true) {
        // 分页逻辑
        this.allocationList = [];
        this.page = 1;
        this.isFinish = false;
        setTimeout(() => {
          this.scroll();
        }, 50);
        this.getList();
      } else {
        if (this.$axMethod[this.url] || this.$api[this.url]) {
          this.allocationList = [];
        }
        this.getList();
      }
    },
    // 处理数据
    dealData(res) {
      this.loading = false;
      if (res.data.code === 200) {
        if (this.isPage === true) {
          if (this.isLoading) {
            this.allocationList.pop(); // loading需要删除一行
          }
          this.allocationList = this.allocationList.concat(res.data.data.data);
          if (res.data.data.last_page === res.data.data.current_page) {
            this.isFinish = true;
            this.allocationList.push({
              isFinish: true,
            });
          }
          this.isLoading = false;
        } else {
          this.allocationList = res.data.data;
        }
      }
    },
    // 获取货位列表
    getList() {
      var params = this.$publicFution.deepCopy(this.vxeParams);
      params.allocation_name = this.query;
      if (this.isPage === true) {
        // 分页逻辑
        params.page = this.page;
        params.per_page = 20;
        params.is_page = 1;
      }
      this.loading = true;
      var resultAxios = this.$axMethod[this.url]
        ? this.$axMethod[this.url]
        : this.$api[this.url];
      if (typeof resultAxios === "string") {
        this.$axios({
          url: resultAxios,
          method: "get",
          params: params,
        }).then((res) => {
          this.dealData(res);
        });
      } else {
        resultAxios(params).then((res) => {
          this.dealData(res);
        });
      }
    },
  },
};
</script>

<style lang='less' scoped>
/deep/ .el-loading-spinner {
  margin-top: -8px;
}
</style>