import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import XEUtils from 'xe-utils'
import VXEUtils from 'vxe-utils'

import { sum } from 'common/utils/index';
import VXETable from "common/components/packages/vxe-table/index";
import axios from 'common/utils/axios';
import { compilePsworld, Logout } from 'common/utils/fetch'
import userDefined from "common/components/plugin/test.js";// erp 全局组件
import publicFution from "common/utils/public.js";
import commonApi from "common/utils/public_function/commonApi.js";
import filters from "common/filters/index.js";
import 'common/styles/common.less' // 全局通用样式


const install = function (Vue) {
    const requireAll = require.context('./', true, /\index.vue$/)
    requireAll.keys().forEach(element => {
        Vue.component(requireAll(element).default.name || requireAll(element).name, requireAll(element).default || requireAll(element))
    })
    // 全局过滤器
    Object.keys(filters).forEach(key => {
        Vue.filter(key, filters[key]);
    });
    Vue.prototype.$sum = sum;
    Vue.prototype.$axios = axios
    Vue.prototype.$Logout = Logout
    Vue.prototype.$compilePsworld = compilePsworld
    Vue.prototype.$publicFution = publicFution;
    Vue.prototype.$common = commonApi;
    Vue.use(VXETable);
    Vue.use(ElementUI);
    Vue.use(userDefined);
    Vue.use(VXEUtils, XEUtils)
    Vue.prototype.$api && commonApi.initApi(Vue.prototype.$api)
}
// 判断是否是直接引入文件
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}
export default install