
<template>
  <div>
    <el-dialog :close-on-click-modal="false" :title="title" @close="close" :visible.sync="dialogEmployeeImportVisible" width="400px">
      <el-upload class="upload-demo" :on-exceed="handleExceed" :before-upload="beforeAvatarUpload" :limit="1" ref="upload" :action="uploadUrl" :headers="headersToken" :data="cdataPost" name="excel" :onError="uploadError" :onSuccess="uploadSuccess" :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
        <div slot="tip" class="el-upload__tip">按照下载模板完成上传，只支持Excel文件</div>
        <slot name="content" class="el-upload__tip"></slot>
      </el-upload>
      <div class="dialog-footer">
        <el-button @click="close">取消</el-button>
        <el-button type="primary" @click="submitUpload">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { serverMoudle } from "common/utils/fetch"; //计算sign的
export default {
  name: "upload",
  props: ["options", "noTips"],
  data() {
    return {
      headersToken: {
        token: localStorage.token
      },
      uploadUrl: this.$axios.defaults.baseURL + this.options.url,
      title: this.options.title,
      dialogEmployeeImportVisible: false
    };
  },
  computed: {
    cdataPost: function() {
      var md5Sign = serverMoudle(this.options.params);
      return {
        ...this.options.params,
        sign: md5Sign
      };
    }
  },
  methods: {
    close() {
      this.$refs.upload.clearFiles();
      this.dialogEmployeeImportVisible = false;
    },
    stateOpen() {
      this.dialogEmployeeImportVisible = true;
    },
    handleExceed(files, fileList) {
      this.$message({
        message: "一次只能上传一个文件！",
        type: "error"
      });
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    uploadSuccess(response) {
      if (response.code == 200) {
        if (!this.noTips) {
          this.$message({
            message: response.msg ? response.msg : response.data,
            type: "success"
          });
        }
        this.sendMsgToParent(response);
        this.dialogEmployeeImportVisible = false;
        this.$emit("uploadFail", response);
      } else {
        this.$message({
          message: response.msg,
          type: "error"
        });
        this.$emit("uploadFail", response);
        this.$refs.upload.uploadFiles[0].status = 'ready' // 上传失败仍可继续上传
        return false;
      }
    },
    sendMsgToParent(response) {
      this.$emit("receiveUploadMsg", response);
    },
    uploadError(response) {
      this.$message({
        message: "上传失败，请重试！",
        type: "error"
      });
      this.sendMsgToParent(response);
    },
    beforeAvatarUpload(file) {
      var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
      const extension = testmsg === "xls";
      const extension2 = testmsg === "xlsx";
      if (!extension && !extension2) {
        this.$message({
          message: "上传文件只能是 xls、xlsx格式!",
          type: "warning"
        });
      }
      return extension || extension2;
    }
  }
};
// 使用说明
// @receiveUploadMsg  //上传状态回调函数(成功失败都返回)
// :options   //title url
//初始化打开this.$refs.upload.stateOpen();
</script>

