//集采业务
// 这是业务的路由代码
export default {
  path: "/service_station_goods",
  component: () => import("@/views/appNav.vue"),
  meta: {
    requireAuth: true
  },
  children: [
    {
      path: "service_station_inventory_goods",
      name: "service_station_inventory_goods",
      component: () => import(
        "@/components/ServiceStation/inventory/companyGoodsInfoList.vue"
      ),
      meta: {
        _meauname: "公司商品信息 ",
        keepAlive: true
      }
    },
    {
      path: "service_station_inventory_goods/service_station_inventory_goods_add",
      name: "service_station_inventory_goods_add",
      component: () => import(
        "@/components/ServiceStation/inventory/companyGoodsInfoAdd.vue"
      ),
      meta: {
        _meauname: "公司商品信息 ",
        keepAlive: true
      }
    },
    {
      path: "service_station_inventory_goods/service_station_inventory_goods_see",
      name: "service_station_inventory_goods_see",
      component: () => import(
        "@/components/ServiceStation/inventory/companyGoodsInfoSee.vue"
      ),
      meta: {
        _meauname: "公司商品信息 ",
        keepAlive: true
      }
    },
    {
      path: "service_station_goods_type_list",
      name: "service_station_goods_type_list",
      component: () => import(
        "@/components/element/system/info/goods/goodsattribute.vue"
      ),
      meta: {
        _meauname: "商品类型管理",
      }
    },
    {
      path: "service_station_goods_brand_list",
      name: "service_station_goods_brand_list",
      component: () => import(
        "@/components/element/system/info/goods/goodsbrand.vue"
      ),
      meta: {
        _meauname: "品牌管理"
      }
    },
    {
      path: "service_station_goods_unit_list",
      name: "service_station_goods_unit_list",
      component: () => import("@/components/element/system/info/goods/unit.vue"),
      meta: {
        _meauname: "单位管理"
      }
    },
    {
      path: "service_station_stockWarningQuery",//库存预警表
      name: "service_station_stockWarningQuery",
      component: () => import("@/components/ServiceStation/InventoryWarn/setInventoryWarnning/stockEarlyWarningQuery.vue"),
      meta: {
        _meauname: "库存预警值"
      }
    },
    {
      path: "service_station_stockEarlyWarning",
      name: "service_station_stockEarlyWarning",
      component: () => import("@/components/ServiceStation/InventoryWarn/inventoryWarning/stockEarlyWarningIndex.vue"),
      meta: {
        _meauname: "库存报警设置"
      }
    }
  ]
}