<template>
  <!-- 品牌 -->
  <span>

    <el-autocomplete v-model="query" v-if='type == 2' value-key='brand_name' @input="queryChange" :fetch-suggestions="(queryString, callback) => getBrand(queryString,callback)" @select="change" placeholder="请输入品牌">
      <template slot-scope="{ item }">
        <div v-if="item.isFinish && list.length < 2" style="textAlign: center;color:#dedede" @click.stop.prevent="">暂无数据</div>
        <div v-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          <i class='icon el-icon-loading'></i> 拼命加载中
        </div>
        <div v-else>{{item.brand_name}}</div>
      </template>
    </el-autocomplete>

    <el-select v-model="value" v-else filterable remote clearable :disabled='disabled' popper-class='brandPopperClass' @clear='change("")' @change="change" reserve-keyword placeholder="请选择品牌" @focus="getBrand('')" :remote-method="getBrand">
      <ul class='selectTitle'>
        <li :class='key + "Class"' v-for="(val,key) in showObj" :key='key'>{{ val }}</li>
      </ul>
      <el-option v-for="item in list" :key="item.brand_id" :label="item.brand_name" :value="item.brand_id">
        <div v-if='item.loading' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          <i class='icon el-icon-loading'></i> 拼命加载中
        </div>
        <div v-else-if='item.isFinish' @click.stop.prevent="" style='text-align: center;color:#409eff'>
          已经到底了
        </div>
        <ul class='selectBody'>
          <li :class='key + "Class"' :title="item[key]" v-for="(val,key) in showObj" :key='key'>{{ item[key] }}</li>
        </ul>
      </el-option>
    </el-select>
  </span>
</template>

<script>
export default {
  name: "getServiceBrand",
  data() {
    return {
      list: [],
      loading: false,
      isLoading: false, // 是否加载中
      isFinish: false, // 是否滚动到底
      page: 1,
      query: "",
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: Number,
      default: 1,
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      },
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      },
    },
    showObj: {
      // 展示label 和id
      type: Object,
      default: () => {
        return {
          brand_name: "名称",
          goods_type_name: "商品类型",
        };
      },
    },
    modelLabel: {
      type: String,
      default: "brand_name",
    },
    modelValue: {
      type: String,
      default: "brand_id",
    },
  },
  watch: {
    value() {
      this.init();
    },
  },
  mounted() {
    this.isScroll = false;
    this.init();
  },
  methods: {
    clear() {
      this.query = "";
    },
    init() {
      if (this.value === 0) {
        this.value = "";
        this.$emit("input", "");
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value !== undefined &&
        this.value !== null &&
        this.value !== ""
      ) {
        if (!this.list.some((val) => val[this.modelValue] === this.value)) {
          this.list.push({
            brand_id: this.value,
            brand_name: this.fatherParams[this.modelLabel],
          });
        }
      }
    },
    scroll() {
      let box = document.getElementsByClassName("brandPopperClass");
      console.log(box);
      let currentSelect,
        that = this;
      for (var i = 0; i < box.length; i++) {
        if (box[i].style.display !== "none") {
          currentSelect = box[i];
        }
      }
      if (currentSelect) {
        currentSelect = currentSelect.getElementsByClassName(
          "el-select-dropdown__wrap"
        )[0];
        currentSelect.onscroll = function () {
          if (
            currentSelect.scrollTop + currentSelect.clientHeight + 200 >
              currentSelect.children[0].clientHeight &&
            !that.isLoading &&
            !that.isFinish
          ) {
            that.page++;
            that.list.push({
              loading: true,
            });
            that.getList();
            that.isLoading = true;
          }
        };
      }
    },
    getBrand(query, callBack) {
      this.$emit("focus");
      this.page = 1;
      this.query = query;
      this.list = [];
      this.isFinish = false;
      this.getList(callBack);
    },
    getList(callBack) {
      var params = this.$publicFution.deepCopy(this.searchParams);
      params = Object.assign(
        { brand_name: this.query, page: this.page, per_page: 20 },
        params
      );
      this.loading = true;
      this.$axios({
        url: "open/GoodsBrand/getBrandList",
        method: "get",
        params: params,
      })
        .then(({ data }) => {
          if (data.code === 200) {
            if (this.isLoading) {
              this.list.pop(); // loading需要删除一行
            }
            this.list = this.list.concat(data.data.data);
            if (data.data.last_page <= data.data.current_page) {
              this.isFinish = true;
              this.list.push({
                isFinish: true,
              });
            }
            this.isLoading = false;
            callBack && callBack(this.list);
          }
          this.loading = false;
          setTimeout(() => {
            if (this.isScroll == false && this.type === 1) {
              this.isScroll = true;
              this.scroll();
            }
          }, 50);
        })
        .catch(() => {
          this.loading = false;
        });
    },
    queryChange() {
      this.$emit("nameChange", this.query);
    },
    change(val) {
      if (this.type === 2) {
        val = val.brand_id;
      }
      this.$emit("input", val);
      var obj = this.list.filter((item) => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj[this.modelLabel];
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    },
  },
};
</script>

<style lang='less'>
.selectTitle {
  margin: 5px;
  text-align: center;
  &::after {
    content: "";
    display: block;
    clear: both;
  }

  li {
    float: left;
  }
  li:first-child {
    box-sizing: border-box;
    padding-left: 14px;
  }
}
.selectBody {
  &::after {
    content: "";
    display: block;
    clear: both;
  }

  li {
    float: left;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.brand_nameClass {
  width: 200px;
}
.goods_type_nameClass {
  width: 100px;
}
</style>