// 渲染器显示模板
function renderCell(h, editRender, scope) {
  var { row, column } = scope
  var that = editRender.getParams ? editRender.getParams(scope) : {}
  if (that.getOutIcon) {
    var b = that.getOutIcon(scope).map(val => {
      return <i class={(val.icon ? val.icon : 'el-icon-search')} title={val.title ? val.title : " "} style='cursor: pointer;float:right;padding:0px 5px;' onClick={(event) => { event.preventDefault(); event.stopPropagation(); val.iconMethod && val.iconMethod(scope, event) }}></i>
    });
    return [
      b,
      <span > {row[column.property]}</span>,
      editRender.getOutSlots ? editRender.getOutSlots(scope) : ''
    ]
  } else {
    return [
      <span > {row[column.property]}</span>,
      editRender.getOutSlots && editRender.getOutSlots(scope)
    ]
  }
}
// 键盘事件
function keyupFunction(xTable, tableColumn, row, step, title) {
  var number = 0
  for (var i = 0; i < tableColumn.length; i++) {
    if (number || step) {
      if (tableColumn[i].editRender) {
        xTable.setActiveCell(row, tableColumn[i].property)
        return true
      }
    } else {
      if (tableColumn[i].property === title) {
        number = i
      }
    }
  }
}
VXETable.interceptor.add('event.keydown', (params, event) => {
  //此时被激活单元格不应该被自动关闭，通过返回 false 可以阻止默认的行为。
  if (event.keyCode === 13) {// 重写插件的回车方法 
    var actived = params.$table.editStore.actived
    if (window.vxeKeyupCallBack) {
      window.vxeKeyupCallBack(event.keyCode, actived)
      return false
    }
    if (actived.row) {
      var { column, args } = actived
      var tableColumn = args.$table.tableColumn
      var title = column.property
      var xTable = args.$table
      if (!keyupFunction(xTable, tableColumn, actived.row, false, title)) {
        var one = xTable.$getRowIndex(actived.row)
        var data = xTable.getTableData().fullData
        if (data[one++]) {
          keyupFunction(xTable, tableColumn, data[one++], true)
        }
      }
    }
    return false
  }
  if (event.keyCode === 9) {// 重写插件的回车方法
    event.preventDefault()
    let actived = params.$table.editStore.actived
    if (actived.row) {
      let { column, args } = actived
      let tableColumn = args.$table.tableColumn
      let title = column.property
      let xTable = args.$table
      if (!keyupFunction(xTable, tableColumn, actived.row, false, title)) {
        let one = xTable.$getRowIndex(actived.row)
        let data = xTable.getTableData().fullData
        if (data[one++]) {
          keyupFunction(xTable, tableColumn, data[one++], true)
        }
      }
    }
    return false
  }
})

VXETable.renderer.add('vNumber', { //自定义渲染器
  // 可编辑激活模板
  autofocus: ".custom-input",
  renderEdit(h, editRender, scope, events) {
    var { row, column, $table } = scope
    var that = editRender.getParams ? editRender.getParams(scope) : {}
    let a;
    a = <input class="vxe-default-input custom-input" type="number" value={row[column.property]}
      onFocus={evnt => { editRender.events.focus && editRender.events.focus(scope, evnt) }}
      onChange={evnt => { editRender.events.change && editRender.events.change(scope, evnt) }}
      onBlur={evnt => {
        row[column.property] = commonMethods(editRender.formatter, evnt.target.value);
        editRender.events.input && editRender.events.input(scope, event);
        editRender.events.blur && editRender.events.blur(scope, evnt);
      }}
      onInput={
        evnt => {
          row[column.property] = isNaN(evnt.target.value) ? '' : evnt.target.value;
          editRender.events && editRender.events.input(scope, event); $table.updateStatus(scope)
        }
      } />
    return [<icon iconList={that.getIcon ? that.getIcon(scope) : []} >
      {a}
    </icon>]
  },
  // 可编辑显示模板
  renderCell: renderCell
})
function numberFomatter(type, val) {// 数字校验器
  var result;
  if (val.charAt(0) == 0) {//保留第一位是0
    result = isNaN(Number(val)) ? 0 : val;
  } else {
    result = isNaN(Number(val)) ? 0 : Number(val);
  }
  if (type === 'Positive') {
    if (result < 0) {
      Message.warning('只能输入正数')
      return 0
    }
  }
  return result
}
VXETable.renderer.add('number', {//自定义渲染器 
  // 可编辑激活模板
  autofocus: ".custom-input",
  renderEdit(h, editRender, scope, events) {
    var { row, column, $table } = scope
    var that = editRender.getParams ? editRender.getParams(scope) : {}
    var a
    a = <input class="vxe-default-input custom-input" type="number" value={row[column.property]}
      v-integerNumber
      onChange={(event) => {
        row[column.property] = numberFomatter(editRender.formatter, event.target.value);
        editRender.events && editRender.events.change && editRender.events.change(scope, event)
      }
      }
      onBlur={evnt => {
        row[column.property] = numberFomatter(editRender.formatter, evnt.target.value);
        editRender.events && editRender.events.blur && editRender.events.blur(scope, evnt);
      }}
      onInput={
        evnt => {
          row[column.property] = isNaN(evnt.target.value) ? '' : evnt.target.value;
          editRender.events && editRender.events.input && editRender.events.input(scope, event); $table.updateStatus(scope)
        }
      } />
    return [
      <icon iconList={that.getIcon ? that.getIcon(scope) : []} scopedSlots={{
        default: () => {
          return (
            a
          );
        },
        content: () => {
          return editRender.getSlots ? editRender.getSlots(scope) : '';
        }
      }} >
        ,
      </icon>
    ]
  },
  // 可编辑显示模板
  renderCell: renderCell
})
// 货位(盘点)
