// vxe表格end
function commonMethods(type, val) {
  if (val === '') {
    return ''
  }
  if (type === "Int") {
    return isNaN(parseInt(val)) ? 0 : parseInt(val);
  } else if (type === "positiveInt") {
    if (isNaN(parseInt(val)) || val < 0) {
      Message.warning('请输入大于等于0的整数')
      return ''
    }
    return parseInt(val)
  } else if (type === "String") {
    return val
  } else if (type === "Positive") {
    var result = isNaN(Number(val)) ? -1 : Number(val).toFixed(2);
    if (result < 0) {
      Message.warning('请输入大于等于0的数')
      return ''
    }
    return isNaN(Number(val)) ? '' : Number(val).toFixed(2);
  } else if (type === "positive") { // 小写诗正数 大于零的数
    var result = isNaN(Number(val)) ? '' : Number(val).toFixed(2);
    if (result <= 0) {
      Message.warning('请输入大于0的数')
      return ''
    }
    return isNaN(Number(val)) ? '' : Number(val).toFixed(2);
  } else {
    if (val >= 1e8) {
      Message.warning("数值不能超过一亿");
      return ''
    }
    return isNaN(Number(val)) ? '' : Number(val).toFixed(2);
  }
}
VXETable.renderer.add('Vinput', {//自定义渲染器
  // 可编辑激活模板
  autofocus: ".custom-input",
  renderEdit(h, editRender, scope) {
    var { row, column, $table } = scope, a
    var that = editRender.getParams ? editRender.getParams(scope) : {}
    if (editRender.events) { // input改变 不防抖
      a = <input class="vxe-default-input custom-input" type="text" placeholder={editRender.placeholder ? editRender.placeholder : ''}
        value={row[column.property]}
        // 失去焦点自动
        disabled={editRender.disabled || that.disabled}
        onFocus={evnt => { editRender.events.focus && editRender.events.focus(scope, evnt) }}
        onChange={evnt => { row[column.property] = commonMethods(editRender.formatter, evnt.target.value); editRender.events.change && editRender.events.change(scope, evnt) }}
        onBlur={evnt => {
          row[column.property] = commonMethods(editRender.formatter, evnt.target.value); editRender.events.input && editRender.events.input(scope, event);
          editRender.events.blur && editRender.events.blur(scope, evnt);
        }}
        onInput={evnt => { row[column.property] = evnt.target.value.trim(); editRender.events.input && editRender.events.input(scope, event); $table.updateStatus(scope) }} />
    } else {
      a = <input class="vxe-default-input custom-input" type="text" placeholder={editRender.placeholder ? editRender.placeholder : ''}
        value={row[column.property]}
        // 失去焦点自动
        disabled={editRender.disabled || that.disabled}
        onInput={evnt => { row[column.property] = evnt.target.value.trim(); $table.updateStatus(scope) }}
        onBlur={evnt => {
          row[column.property] = commonMethods(editRender.formatter, evnt.target.value);
        }} />
    }
    return [
      <icon iconList={that.getIcon ? that.getIcon(scope) : []} scopedSlots={{
        default: () => {
          return (
            a
          );
        },
        content: () => {
          return editRender.getSlots ? editRender.getSlots(scope) : '';
        }
      }} >
        ,
      </icon>
    ]
  },
  // 可编辑显示模板
  renderCell: renderCell
})
