// select 下拉基于elementui封装
VXETable.renderer.add('vSelect', {
  autofocus: ".el-input__inner",
  renderEdit(h, editRender, scope) {
    var that = editRender.getParams ? editRender.getParams(scope) : {}
    return [<vSelect clearable={that.clearable} scope={scope} popperClass={that.popperClass} isPage={that.isPage} url={that.url} extendParams={that.extendParams} options={that.list} returnString={that.returnString} dealList={that.dealList} modelLabel={that.modelLabel} modelKey={that.modelKey} vxeParams={that.params} itemObj={that.itemObj} showObj={that.showObj} change={editRender.events ? editRender.events.change : null} > </vSelect>]
    // return [<vSelect scope={scope} url={that.url} vxeParams={that.params} customTitleList={that.customTitleList} isInitList={that.isInitList} itemObj={that.itemObj} changeFn={editRender.events ? editRender.events.change : null} > </vSelect>]
  },
  // 可编辑显示模板
  renderCell(h, editRender, scope) {
    var { row, column } = scope, msg = row[column.property]
    var that = editRender.getParams ? editRender.getParams(scope) : {}
    if (editRender.slots) {
      return editRender.slots(scope)
    }
    if (that.list !== undefined && that.list.length > 0) {
      var rowId = that.itemObj ? that.itemObj.id : 'value'; // 每一行的id 值  与下面一致就绑定一列
      var bindId = that.modelKey ? that.modelKey : (that.itemObj ? that.itemObj.id : 'value') // v-model绑定的id
      var box = that.list.filter(obj => obj[rowId] === row[bindId])
      if (box.length > 0) {
        msg = box[0][that.modelLabel ? that.modelLabel : (that.itemObj ? that.itemObj.name : 'label')]
        row[column.property] = msg
      }
    }
    if (that.getOutIcon) {
      var b = that.getOutIcon(scope).map(val => {
        return <i class={'iconfont ' + (val.icon ? val.icon : 'el-icon-search')} style='cursor: pointer;float:right;padding:0px 5px;' onClick={(event) => { event.preventDefault(); event.stopPropagation(); val.iconMethod && val.iconMethod(scope, event) }}></i>
      });
      return [
        b,
        <span > {msg}</span>
      ]
    } else {
      return [
        <span > {msg}</span>
      ]
    }
  }
})

export default VXETable