import upload from './upload'
import JybdCascader from './cascader'
import JybdGoodslist from './goodsList'
import xTable from './vxeTable/xTable'
import goodsPopout from './goodsPopout/goodsPopout.vue'
import vGoodsList from './vGoodsList'
import jybdupload from './upload/index.vue'
import excelExport from './upgradeExcel/upgradeExcel.vue';
const install = function (Vue) {
  if (install.installed) return;
  Vue.component(upload.name, upload)
  Vue.component(jybdupload.name, jybdupload)
  Vue.component(xTable.name, xTable)
  Vue.component(JybdCascader.name, JybdCascader)
  Vue.component(JybdGoodslist.name, JybdGoodslist)
  Vue.component(goodsPopout.name, goodsPopout)
  Vue.component(vGoodsList.name, vGoodsList)
  Vue.component(excelExport.name, excelExport)
}
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
};
export default {
  install,
  xTable,
  upload,
  JybdCascader,
  JybdGoodslist,
  jybdupload,
  vGoodsList,
  goodsPopout,
  excelExport
}