import axios from "common/utils/axios";
// import Router from '@/router'
import store from '@/store'

function setPermission (router) {
  var onceTime = 1;
  router.beforeEach((to, from, next) => {
    if (to.meta.iframe) {
      store.dispatch('showIframeAct', to.name)
    }
    if (to.fullPath.split("/")[1] == 'report') {//埋点报表使用情况
      axios({
        method: "get",
        url: 'v2/open/ReportStatistic/add',
        params: {
          code: to.fullPath.split("/")[2]
        }
      })
    }
    window.localStorage.titName = to.meta._meauname;
    window.vxeKeyupCallBack = null
    var str = to.path;
    var objArr = str.split("/", 3);
    store.dispatch('this_Path', str)
    var getToken = window.localStorage.getItem("token")
    if (to.matched.some(r => r.meta.requireAuth)) {
      if (getToken) {
        if (!["home", "Checkstand"].includes(objArr[objArr.length - 1])) {
          axios({//判断menu权限
            method: "get",
            url: "open/RequestOpen/getRequestOpenList",
            params: {
              code: objArr[2]
            }
          }).then(function (res) {
            // next(); // 开发方便用的，上线去掉
            if (res.data.code == 200) {
              if (res.data.data.length != 0) {
                if (res.data.data.is_role == 'Y') {
                  localStorage.removeItem("authority")
                  let authority = JSON.stringify(res.data.data.role_list);
                  localStorage.setItem("authority", authority)
                  if (onceTime == 1) {
                    (() => {
                      var s = to.path.split("/", 3).join('/');
                      if (s === to.path) {// 相同路径传递参数
                        next({
                          path: s,
                          query: to.query
                        });
                      } else {
                        next({
                          path: s
                        });
                      }
                      onceTime++
                    })()
                  }
                  next();
                }
              }
              next();
            }

          })
          //操作日志-业务日志和上机日志由权限控制，包括角色的查看权限和系统的功能模块权限，如果没有相关权限，则不可查看日志；
          //如果接口返回status为1， 那么就必须是审核之后才可以进行打印
          // axios({
          //   method: "get",
          //   url: "open/BillSetting/getSettingInfo",
          //   params: {
          //     type: 1,
          //   }
          // }).then(res => {
          //   //true 需要审核后打印
          //   if (res.code == 200) {
          //     localStorage.removeItem("hasAuditorStatusPrint");
          //     localStorage.hasAuditorStatusPrint = res.data.data[0].status;
          //   }
          // });

        } else {
          next();
        }


      } else {
        next({
          path: "/login"
        })
        // window.location.replace('/#/login')
      }
    } else {
      onceTime++
      next();
    }
  });

  // export default router;
  // 页面刷新时，重新赋值token
  if (window.localStorage.getItem("token")) {
    // store.commit(types.LOGIN, window.localStorage.getItem("token"));
    store.dispatch('LOGIN', window.localStorage.getItem("token"))
  }
}

export default setPermission




