<template>
  <!-- 来源服务器 -->
  <el-select v-model="value" placeholder="请选择" filterable :disabled='disabled' @focus="getList" @change="change">
    <el-option v-for="item in list" :key="item.value" :label="item.label" :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "sourceServer",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "label"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      // if (this.fatherParams && this.fatherParams[this.modelLabel]) {
      //   this.list.push({
      //     supplier_id: this.value,
      //     supplier_name: this.fatherParams[this.modelLabel]
      //   });
      // }
    },
    getList() {
      this.$axios
        .axios({
          url: "/open/Warehouse/getWarehouseList",
          method: "get",
          params: this.searchParams
        })
        .then(({ data }) => {
          if (data.cdoe === 200) {
            this.list = data.data;
          }
        });
    },
    change(val) {
      this.$emit("input", val);
      this.$emit("change", val);
    }
  }
};
</script>

