<template>
  <div>
    <el-link type="primary" @click="exportClick()">导出</el-link>
  </div>
</template>

<script>
export default {
  name: "excelExport",
  data() {
    return {};
  },
  props: {
    titles: {
      type: String,
      default: "错误文件"
    },
    headerTitle: {
      type: Array,
      default() {
        return [];
      }
    },
    dataList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    exportClick() {
      require.ensure([], () => {
        try {
          const { export_json_to_excel } = require("common/components/plugin/upgradeExcel/Export2Excel.js");
          const data = this.formatJson(this.dataList);
          export_json_to_excel(this.headerTitle, data, this.titles);
        } catch (error) {
          console.error(error);
        }
      });
    },
    formatJson(jsonData) {
      let largeArrays = [];
      jsonData.forEach(element => {
        largeArrays.push(Object.values(element));
      });
      console.log(largeArrays)
      return largeArrays;
    }
  }
};
</script>

