<template>
  <!-- 业务员 -->
  <el-select v-model="value" placeholder="请选择" @clear='change("")' clearable filterable :disabled='disabled' @focus="getList('')" @change="change">
    <el-option v-for="item in list" :key="item.employee_id" :label="item.employee_name" :value="item.employee_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getBuyer",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "employee_name"
    },
    modelValue: {
      type: String,
      default: "employee_id"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (this.fatherParams && this.fatherParams[this.modelLabel] && this.value !== undefined &&
        this.value !== null &&
        this.value !== "") {
        if (!this.list.some(val => val[this.modelValue] === this.value)) {
          this.list.push({
            employee_id: this.value,
            employee_name: this.fatherParams[this.modelLabel]
          });
        }
      } else if (this.value === 0 && !this.fatherParams[this.modelLabel]) {// 解决采购员id为0的情况
        this.value = ''
      }
    },
    getList(query) {
      this.searchParams.employee_name = query;
      this.$axios({
        url: "purchase_center/Open/Employee/getEmployeeList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.employee_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {// 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

