export default {
  methods: {
    initTool() {
      // 工具栏基本配置
      var tool = {
        id: "Jybd",
        buttons: [
          // {
          //   name: "数据导出",
          //   dropdowns: [
          //     // { code: "input", name: "导入.csv" },
          //     { code: "export", name: "导出.csv" }
          //   ]
          // }
        ],
        // refresh: true, // 刷新按钮
        zoom: true,
        resizable: {
          storage: true
        },
        setting: {
          // 动态列配置
          storage: true, // 是否存在本地
          immediate: true // 是否实时生效
        }
      };
      // 每个工具需要唯一id //如果一个页面两个表格需要改变此方法
      if (this.$router) {
        var route = this.$router.currentRoute.fullPath.split("/");
        if (route[3]) {
          if (route[3].indexOf('?') !== -1) {
            tool.id = route[3].slice(0, route[3].indexOf('?') - 1)
          } else {
            tool.id = route[3]
          }
        } else {
          tool.id = route[2]
        }
        if (this.isShowTool) {
          this.tool_Config = Object.assign(tool, this.toolConfig);
        }
      }
    }
  },
}