import axios from "common/utils/axios";
export default {
  getServiceCollectionList: "/v2/custom/OfflineCollection/getCollectionList",
  service_getOrderDetailed: '/v2/custom/OfflineCollection/getOrderDetailed',
  getVisibleCompany(params) {
    return axios({
      url: "/v2/open/Company/getVisibleCompany",
      method: "get",
      params
    });
  },
  // 获取适配车型
  getAttributeCarLibraryList(params) {
    return axios({
      url: "/service_station/open/Goods/getAttributeCarLibraryList",
      method: "get",
      params
    });
  },
  //审核订单
  service_auditorPass(data) {
    return axios({
      url: "/v2/custom/OfflineCollection/auditorPass",
      method: "post",
      data
    });
  },
  service_auditorRefuse(data) {
    return axios({
      url: "/v2/custom/OfflineCollection/auditorRefuse",
      method: "post",
      data
    });
  }, 
  getStocksWaringGoodsList(params) {
    return axios({
      url: "/service_station/open/Goods/getStocksWaringGoodsList",
      method: "get",
      params
    });
  },
  getShoppingCartListAllPrice(params) {
    return axios({
      url: "/service_station/purchase/ShoppingCart/getShoppingCartList",
      method: "get",
      params
    });
  }

}