<template>
  <!-- 仓库渲染器 -->
  <el-select v-if="show" clearable v-model="scope.row[itemObj['id']]" placeholder="请选择" @focus="getList(scope)" @change="value => {changeFn(scope, list, value)}" :loading="loading">
    <el-option v-for="(item, index) in list" :label="item[itemObj['name']]" :value="item[itemObj['id']]" :key="item.index">
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      loading: false,
      show: false
    };
  },
  props: {
    // 请求列表接口时参数
    vxeParams: {
      type: Object,
      default: () => {}
    },
    // 当前行的数据
    scope: {
      type: Object,
      default: () => {}
    },
    // focus 要执行的方法
    url: {
      type: String,
      default: "getWarehouseFocus"
    },
    // 触发 select change
    changeFn: {
      type: Function,
      default: function() {}
    },
    // for 循环使用字段
    itemObj: {
      type: Object,
      default: () => {
        return {
          id: "warehouse_id",
          name: "warehouse_name"
        };
      }
    }
  },
  created() {
    this.getList(this.scope);
  },
  methods: {
    // 获取下拉列表
    getList(scope) {
      let { row } = scope
      if (this.list.length > 0) {
        return;
      }
      this.loading = true;
      let params = Object.assign(
        {},
        {
          ...this.vxeParams
        }
      );
      this.$axMethod[this.url](params)
        .then(res => {
          this.loading = false;
          if (res.data.code === 200) {
            this.list = res.data.data;
            this.show = true;
          }
        })
        .catch(error => {
          this.loading = false;
        });
    }
  }
};
</script>

