<template>
  <div>
    <el-cascader placeholder="搜索" ref="cascader" :options="authData" filterable clearable :props="{ value: 'value' }" remote v-model="cascaderVue" @change="handleChange"></el-cascader>
  </div>
</template>

<script>
export default {
  name: "JybdCascader",
  props: {
    cascaderVue: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      clearable: true,
      authData: [],
      options: [],
    };
  },
  methods: {
    handleChange(val) {
      setTimeout(() => {
        this.$emit(
          "cascaderChange",
          val,
          this.$refs.cascader.getCheckedNodes()
        );
      }, 50);
    },
    operationModule() {
      this.$axMethod.getGoodsTypeList().then(({ data }) => {
        if (data.code == 200) {
          var authList = data.data;
          authList.forEach((item, index) => {
            if (item.sub.length) {
              this.authData.push({
                value: item.goods_type_id,
                label: item.goods_type_name,
                children: [],
              });
              item.sub.forEach((Items, idx) => {
                if (Items.sub.length) {
                  this.authData[index].children.push({
                    value: Items.goods_type_id,
                    label: Items.goods_type_name,
                    children: [],
                  });
                  Items.sub.forEach((element) => {
                    this.authData[index].children[idx].children.push({
                      value: element.goods_type_id,
                      label: element.goods_type_name,
                    });
                  });
                } else {
                  this.authData[index].children.push({
                    value: Items.goods_type_id,
                    label: Items.goods_type_name,
                  });
                }
              });
            } else {
              this.authData.push({
                value: item.goods_type_id,
                label: item.goods_type_name,
              });
            }
          });
        }
      });
    },
    clear() {
      this.cascaderVue = [];
    },
    // 使用说明
    // this.$refs.cascader.clear()//清楚选择项，用在重置
  },
  mounted() {
    this.operationModule();
  },
};
</script>

<style lang="less" scoped></style>
