<template>
  <el-select  v-model="value" :clearable="clearable" @clear='change("")' remote :disabled='disabled' @focus="getList('')" @change="change" filterable maxlength="100" :remote-method="getList">
    <ul class="customer customer-ul4 customer-ul2">
      <li>客户编号</li>
      <li>客户名称</li>
      <li>联系人</li>
      <li>联系电话</li>
      <li>区域</li>
    </ul>
    <el-option v-for="item in list" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
      <ul class="customer customer-ul4">
        <li :title="item.customer_id">{{ item.customer_id }}</li>
        <li :title="item.customer_name">{{ item.customer_name }}</li>
        <li :title="item.contact_name">{{item.contact_name}}</li>
        <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
        <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
      </ul>
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getServiceUser",
  data() {
    return {
      list: [],
      isChange: false // 是否是change引起的变动
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelValue: {
      type: String,
      default: "customer_id"
    },
    modelLabel: {
      type: String,
      default: "customer_name"
    },
    url: {
      type: String,
      default: "/open/Customer/getCustomerList"
    }
    // resultStr: {
    //   type: String,
    //   default: "company_list"
    // }
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            customer_id: this.value,
            customer_name: this.fatherParams[this.modelLabel]
          }
        ];
      }
    },
    _setList(list) {
      this.list = this.list.concat(list);
    },
    getList(query) {
      this.searchParams.customer_name = query;
      this.searchParams.hideLoding = true;
      this.list = [];
      this.$axios({
        method: "get",
        url: this.url,
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.customer_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

