import axios from "common/utils/axios";
export default {
  // 账号明细列表
  serviceStationMyAccountManagementGetInfo: '/service_station/system/MyAccountManagement/getInfo',
  // 发送验证码
  serviceStationMyAccountManagementSendCode(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/sendCode",
      method: "post",
      data: data
    });
  },
  // 确认修改验证码
  serviceStationMyAccountManagementModifyPaypwd(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/modifyPaypwd",
      method: "post",
      data: data
    });
  },

  // 保存收款码
  serviceStationMyAccountManagementSaveImageUpload(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/saveImageUpload",
      method: "post",
      data: data
    });
  },

  // 清楚收款码
  serviceStationMyAccountManagementCleanImage(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/cleanImage",
      method: "post",
      data: data
    });
  },

  // 获取手机号
  serviceStationCapitalGetPhone(data) {
    return axios({
      url: "/service_station/open/Capital/getPhone",
      method: "get",
      params: data
    });
  },
  // 小程序店铺列表
  serviceStationMyAccountManagementStoreList(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/storeList",
      method: "get",
      params: data
    });
  },
  // 获取线下汇款充值信息
  serviceStationGetRechargeInfo(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/getRechargeInfo",
      method: "get",
      params: data
    });
  },
  // 小程序店铺详情
  serviceStationMyAccountManagementStoreInfo(data) {
    return axios({
      url: "/service_station/system/MyAccountManagement/storeInfo",
      method: "get",
      params: data
    });
  },
}