<template>
  <!-- erp供应商 -->
  <el-select v-model="value" placeholder="请选择单位" clearable @clear='change("")' :disabled='disabled' filterable @focus="getList('')" remote :remote-method="getList" @change="change">
    <el-option v-for="item in list" :key="item.unit_id" :label="item.unit_name" :value="item.unit_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getUnitName",
  data() {
    return {
      list: [],
      isChange: false, // 是否是change引起的变动
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: { default: "" },
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      },
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      },
    },
    modelLabel: {
      type: String,
      default: "unit_name",
    },
    modelValue: {
      type: String,
      default: "unit_id",
    },
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            unit_id: this.value,
            unit_name: this.fatherParams[this.modelLabel],
          },
        ];
      }
    },
    getList(query) {
      this.searchParams.unit_name = query;
      this.list = [];
      this.$axios({
        url: "open/unit/getUnitList",
        method: "get",
        params: this.searchParams,
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter((item) => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.unit_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    },
  },
};
</script>

