<template>
  <!-- 供应商 -->
  <el-select v-model="value" placeholder="请选择" clearable @clear='change("")' :disabled='disabled' filterable @focus="getList('')" remote :remote-method="getList" @change="change">
    <el-option v-for="item in list" :key="item.supplier_id" :label="item.supplier_name" :value="item.supplier_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getSupplier",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: { default: "" },
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "supplier_name"
    },
    modelValue: {
      type: String,
      default: "supplier_id"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value !== undefined &&
        this.value !== null &&
        this.value !== ""
      ) {
        if (!this.list.some(val => val[this.modelValue] === this.value)) {
          this.list.push({
            supplier_id: this.value,
            supplier_name: this.fatherParams[this.modelLabel]
          });
        }
      } else if (this.value === 0 && !this.fatherParams[this.modelLabel]) {
        // 解决为0的情况
        this.value = "";
      }
    },
    getList(query) {
      this.searchParams.supplier_name = query;
      this.$axios({
        url: "purchase_center/Open/Supplier/getSupplierList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.supplier_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

