<template>
  <!-- 采购申请公司 -->
  <el-select v-model="value" placeholder="请选择组织" :clearable="clearable" @clear='change("")' remote :disabled='disabled' @focus="getList('')" :remote-method="getList" @change="change" filterable maxlength="100">
    <el-option v-for="item in list" :key="item.company_id" :label="item.company_name" :value="item.company_id">
      <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getCompany",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      type: Object,
      default: () => {
        return {};
      }
    },
    modelValue: {
      type: String,
      default: "company_id"
    },
    modelLabel: {
      type: String,
      default: "company_name"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value !== undefined &&
        this.value !== null &&
        this.value !== ""
      ) {
        if (!this.list.some(val => val[this.modelValue] === this.value)) {
          this.list.push({
            company_id: this.value,
            company_name: this.fatherParams[this.modelLabel]
          });
        }
      } else if (this.value === 0 && !this.fatherParams[this.modelLabel]) {
        // 解决为0的情况
        this.value = "";
      }
    },
    getList(query) {
      this.searchParams.company_name = query;
      this.list = [];
      this.$axios({
        url: "/purchase_center/Open/Company/getCompanyList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
          if (query === true && this.list.length > 0) {
            this.change(this.list[0].company_id);
          }
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.company_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

