<template>
  <!-- erp业务员 -->
  <el-select v-loading='loading' v-model="value" :placeholder="title" :disabled='disabled' :clearable='clearable' @clear='change("")' filterable @focus="getList('')" remote @change="change">
    <el-option v-for="item in list" :key="item.employee_id" :label="item.employee_name" :value="item.employee_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "erpEmployee",
  data() {
    return {
      list: [],
      loading: false,
      isChange: false, // 是否是change引起的变动
    };
  },
  props: {
    title: {
      type: String,
      default: "请选择业务员",
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      },
    },
    searchParams: {
      // 查询条件对象
      type: Object,
      default: () => {
        return {};
      },
    },
    modelLabel: {
      type: String,
      default: "employee_name",
    },
    modelValue: {
      type: String,
      default: "employee_id",
    },
  },
  watch: {
    value() {
      if (!this.isChange) {
        this.init();
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (Number(this.value) === 0) {
        this.value = "";
        this.$emit("input", "");
        this.fatherParams[this.modelLabel] = "";
      }
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value
      ) {
        this.list = [
          {
            employee_id: this.value,
            employee_name: this.fatherParams[this.modelLabel],
          },
        ];
      }
    },
    getList(query) {
      this.searchParams.employee_name = query;
      this.searchParams.hideLoding = true;
      this.loading = true;
      this.$axios({
        url: "/v2/open/employee/getEmployeeList",
        method: "get",
        params: this.searchParams,
      })
        .then(({ data }) => {
          if (data.code === 200) {
            this.list = data.data;
          }
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    change(val) {
      this.isChange = true;
      setTimeout(() => {
        this.isChange = false;
      }, 100);
      this.$emit("input", val);
      var obj = this.list.filter((item) => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.employee_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    },
  },
};
</script>

