import axios from "common/utils/axios";
var api = {}
import {
  MessageBox
} from "element-ui";
export function initApi(params) {
  api = params
}
export function getData(arg, type, callback) {
  /*
    * @Date: 2018-07-06 10:23:10
    * 初始化  搜索，重置，切换页码，切换页码长度 都需要调取该方法重新初始化列表
    *  arg格式：
    * {
    *   params：{},//请求参数
        TableData: this.TableData, //列表数据
        API: "getxxx", //接口名
        vm: this //传递的this
    }
    */
  if (arg["params"]) {
    arg["vm"]["goPage"] = "page" in arg["params"] ? arg["params"]["page"] : 1; //改变当前页
    arg["vm"]["pageSize"] =
      "per_page" in arg["params"] ? arg["params"]["per_page"] : 30; //改变当前页size
  }
  arg.vm.loading1 = true;

  axios({
    method: "get",
    url: api[arg.API],
    params: arg.params
  }).then(res => {
    if ([200, 304].includes(res.data.code)) {
      if (type == 2) {
        //有总计的type传2
        arg.TableData.tableData = res.data.data.data;
        arg.TableData.total = Number(res.data.data.total);
        arg.TableData.allList = res.data.data.desc;
      } else if (type == 3) {
        arg.TableData.tableData = res.data.data.data;
        arg.TableData.total = Number(res.data.data.total);
        arg.vm.thisPageBalance = res.data.data.this_page;
        arg.vm.allCountBalance = res.data.data.total_balance;
      } else if (type == 4) {
        arg.TableData.tableData.push.apply(
          arg.TableData.tableData,
          res.data.data.data
        );
      } else {
        arg.TableData.tableData = res.data.data.data === undefined ? res.data.data : res.data.data.data;
        arg.TableData.total = Number(res.data.data.total);
      }
      if (res.data.data.is_shop) {
        arg.vm.is_shop = res.data.data.is_shop;
      }
      callback && callback(res);
    }
    arg.vm.loading1 = false;
    arg.vm.goPage = Number(res.data.data.current_page);
    arg.vm.pageSize = Number(res.data.data.per_page);

  }).catch(error => {
    arg.vm.loading1 = false; // 关闭加载
  });
}

/**
 * 以下三个方法有联动关系，通过最后一个参数是否存在进行控制
 *
 * 存在：级联项清空 不存在：级联项不清空
 *
 *    获取组织下拉
 *    调用方法：getCompanyListCommon(this,this.params,1)
 */
export function getCompanyListCommon(self, obj = "") {
  //获取组织下拉(用于组织列表)
  let params = {
    company_name: obj
  };
  self.$axMethod.getCompanyListDefalut(params).then(({
    data
  }) => {
    var result = data.data.company_list;
    if (result instanceof Array) {
      result.unshift({
        company_name: data.data.company_name,
        company_id: data.data.company_id,
        is_choose: data.data.is_choose,
        type: 1
      });
      self.$set(
        self,
        "companyList",
        result.filter(item => {
          return item.is_choose == 1; // 是否可以被选择 1可以 0不可以
        })
      );
    }
  });
}
/**
 * 获取部门下拉
 * 例如：getDepartmentListCommon(this,this.params,1)
 */
export async function getDepartmentListCommon(self, obj, status) {
  var params = {};
  if (obj && "company_id" in obj) {
    params = {
      company_id: obj.company_id
    };
    await self.$common.options(
      "getDepartmentListOption",
      self.departmentList,
      params
    ); //部门
  } else {
    await self.$common.options("getDepartmentListOption", self.departmentList); //部门
  }
  if ("warehouseList" in self) {
    //如果页面中有仓库数组则调取仓库
    self.$common.options("getWarehouseListOption", self.warehouseList, params);
  }

  if (status) {
    //都是清除部门和业务员字段，由于各模块间名字v-model绑定名字不一样，故都清除
    let arr = [
      "department_id",
      "employee_id",
      "warehouse_id",
      "allocation_id",
      "buyer_id",
      "buyerId",
      "business_id",
      "customer_id"
    ].forEach(item => {
      if (item in obj) self.$set(obj, item, "");
    });
  }
  return Promise.resolve(self.departmentList);
}
