export function CompanySwitchClear(vm, obj) {
  //都是清除部门和业务员字段，由于各模块间名字v-model绑定名字不一样，故都清除
  let arr = [
    "department_id", //部门
    "supplier_id", //供应商
    "employee_id", //质检员
    "warehouse_id", //仓库名称
    "allocation_id", //货位名称
    "buyer_id", //采购员
    "business_id", //业务员
    "customer_id" //客户名称
  ].forEach(item => {
    if (item in obj) vm.$set(obj, item, "");
  });
}
/**
 * 业务员下拉
 * getEmployeeListCommon(this,this.params,1)
 */
export function getEmployeeListCommon(self, obj, status) {
  // "department_id" in obj ||
  if (obj && "company_id" in obj) {
    let params = {
      // department_id: obj.department_id || null,
      company_id: obj.company_id || null
    };
    self.$common.options("getEmployeeListOption", self.employeeList, params); //业务员
  } else {
    self.$common.options("getEmployeeListOption", self.employeeList); //业务员
  }
  if (status) {
    //都是清除业务员字段，由于各模块间名字v-model绑定名字不一样，故都清除
    if ("employee_id" in obj) self.$set(obj, "employee_id", "");
    if ("buyer_id" in obj) self.$set(obj, "buyer_id", "");
    if ("buyerId" in obj) self.$set(obj, "buyerId", "");
    if ("business_id" in obj) self.$set(obj, "business_id", "");
  }
}
// 客户下拉
export function clienteleListCommon(self) {
  self.$axMethod.getCustomerListOpen().then(res => {
    let result = res.data.data;
    let obj = [];
    for (var value of result) {
      obj.unshift({
        customer_name: value.customer_name,
        customer_id: value.customer_id,
        address: value.address
      });
    }
    self.$set(self, "SelesTomer", obj);
  });
}
