//只能是数字类型，保留两位小数
export function handleEditflex(index, val, ag) {
  if (ag == 1) {
    this.$nextTick(() => {
      val.goods_num = Math.ceil(val.goods_num);
    });
  } else if (ag == 0) {
    this.$nextTick(() => {
      val.purchase_price = val.purchase_price.toFixed(2);
    });
  }
}

export function clearTime(val) {
  if (val == null) {
    delete this.searchParams.start_time;
    delete this.searchParams.end_time;
  }
}

export function companyDepartment(vm) {
  //组织和部门默认带出
  let companyList = [{
    company_id: Number(localStorage.company_id),
    company_name: localStorage.company_name
  }];
  vm.$set(vm, "companyList", companyList);
  let departmentList = [{
    department_id: Number(localStorage.department_id),
    department_name: localStorage.department_name
  }];
  vm.$set(vm, "departmentList", departmentList);
  vm.$set(vm.params, "company_id", Number(localStorage.company_id));
  vm.$set(vm.params, "department_id", Number(localStorage.department_id));
}
export function getDepartmentList(self, obj, company, status) {
  if (obj && company in obj) {
    let params = {
      company_id: obj[company]
    };
    self.$axMethod.getDepartmentListOpen(params).then(res => {
      let depart = [];
      for (let val of res.data.data) {
        depart.unshift({
          department_id: val.department_id,
          department_name: val.department_name
        });
      }
      self.$set(self, "departmentList", depart);
    });
  }
  if (status) {
    //都是清除部门和业务员字段，由于各模块间名字v-model绑定名字不一样，故都清除
    self.$set(obj, "department_id", "");
    if ("employee_id" in obj) self.$set(obj, "employee_id", "");
    if ("buyer_id" in obj) self.$set(obj, "buyer_id", "");
    if ("buyerId" in obj) self.$set(obj, "buyerId", "");
    if ("business_id" in obj) self.$set(obj, "business_id", "");
  }
}
