<template>
  <!-- 仓库 -->
  <el-select v-model="value" placeholder="请选择" :disabled='disabled' clearable @clear='change("")' filterable @focus="getList('')" remote :remote-method="getList" @change="change">
    <el-option v-for="item in list" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "getWarehouse",
  data() {
    return {
      list: []
    };
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    value: null,
    fatherParams: {
      // 父组件params 通常是回显用的
      type: Object,
      default: () => {
        return {};
      }
    },
    searchParams: {
      // 查询条件对象
      type: Object,
      default: () => {
        return {};
      }
    },
    modelLabel: {
      type: String,
      default: "warehouse_name"
    },
    modelValue: {
      type: String,
      default: "warehouse_id"
    }
  },
  watch: {
    value() {
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (
        this.fatherParams &&
        this.fatherParams[this.modelLabel] &&
        this.value !== undefined &&
        this.value !== null &&
        this.value !== ""
      ) {
        if (!this.list.some(val => val[this.modelValue] === this.value)) {
          this.list.push({
            warehouse_id: this.value,
            warehouse_name: this.fatherParams[this.modelLabel]
          });
        }
      } else if (this.value === 0 && !this.fatherParams[this.modelLabel]) {
        // 解决为0的情况
        this.value = "";
      }
    },
    getList(query) {
      if (!this.searchParams.customer_id) {
        this.list = [];
        return false;
      }
      this.searchParams.warehouse_name = query;
      this.searchParams.hideLoding = true;
      this.$axios({
        url: "/purchase_center/Open/Warehouse/getWarehouseList",
        method: "get",
        params: this.searchParams
      }).then(({ data }) => {
        if (data.code === 200) {
          this.list = data.data;
        }
      });
    },
    change(val) {
      this.$emit("input", val);
      var obj = this.list.filter(item => item[this.modelValue] === val)[0];
      if (obj) {
        this.$emit("change", obj);
        this.fatherParams[this.modelLabel] = obj.warehouse_name;
      } else {
        this.$emit("change", {});
      }
      if (val === "") {
        // 清空阻止回显
        this.fatherParams[this.modelLabel] = "";
      }
    }
  }
};
</script>

