<template>
  <div>
    <vxe-modal v-model="show" @hide="$emit('close')" id="myModal6" :width="width" :esc-closable='true' destroy-on-close height="700" min-width="100" min-height="100" resize remember storage>
      <template v-slot:title>
        <span>选择商品</span>
      </template>
      <template>
        <el-form :inline="true" :model="params" ref="ruleForm" class="demo-form-inline">
          <el-form-item label="商品名称" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("goods_name")'>
            <el-input v-model="params.goods_name" clearable @keyup.enter.native="onSearch" placeholder="名称、编码、助记码、工厂代码"></el-input>
          </el-form-item>
          <el-form-item label="品牌" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("brand_id")'>
            <getBrand v-model='params.brand_id' />
          </el-form-item>
          <el-form-item label="商品类型" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("shopType")'>
            <jybd-cascader ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("warehouse")'>
            <el-select clearable filterable v-model="params.warehouse_id" @focus="warehouseInvok" placeholder="请选择" :disabled="Boolean(searchParams.warehouse_id)">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="规格型号" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("specification_type")'>
            <el-input v-model="params.specification_type" clearable @keyup.enter.native="onSearch" placeholder="请输入规格型号"></el-input>
          </el-form-item>
          <el-form-item label="花纹" size="mini" :label-width="formStyle.labelWidth" v-if='paramsBox.includes("pattern")'>
            <el-input v-model="params.pattern" clearable @keyup.enter.native="onSearch" placeholder="请输入花纹"></el-input>
          </el-form-item>
          <el-form-item size="mini" :label-width="formStyle.labelWidth">
            <!-- 默认显示库存不为0 传1显示库存为0	 -->
            <el-checkbox v-model="params.is_available_stocks" :true-label='1' v-if='paramsBox.includes("is_stocks")' :false-label='0'>可用库存>0</el-checkbox>
            <!-- 默认显示库存不为0 传1显示库存为0	 -->
            <el-checkbox v-model="params.is_stocks" :true-label='1' :false-label='0' v-if='paramsBox.includes("is_available_stocks")'>在库库存 >0</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
        <div>
          <div class="wz_selectr" style="margin: 15px 0;">【参照商品列表】</div>
          <xTable :xData="tableDataTop" dataStr='tableDataTop' :ajax='{ url: this.url, params: params }' @getData='dealData' :maxHeight="200" :rowId="rowId" @selectChange='selectAllChange' @selectAllChange="selectAllChange" :selectConfig='{reserve: true,highlight: true,trigger:"row"}' ref='xTable' :tableColumn='tableColumnTop' />
        </div>
        <div class="wz_selectr">【选中商品列表】已选<span style="color:red">{{llistlebnth}}</span>种商品</div>
        <div style='height: 160px'>
          <xTable ref='xTables' :isPage="false" :maxHeight="160" :selectConfig='{trigger:"row"}' :tableColumn='tableColumnBottom' :xData="tableData" @selectChange='selectAllChangeTwo' @selectAllChange="selectAllChangeTwo" />
        </div>
        <div class="dialog-footer">
          <el-button @click="$emit('close')">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import { goodsList, paramsModule } from "./goodsList";
export default {
  name: "goodsPopout",
  mixins: [goodsList],
  props: {
    rowId: {
      type: String,
      default: "batch_id",
    },
    width: {
      type: Number,
      default: 1500,
    },
    url: {
      type: String,
      default: "getGoodsListPopup",
    },
    searchParams: Object, // 查询条件
    ExcloudParams: {
      // 参照表格要隐藏的列 字符串
      type: Array,
      default: () => [],
    },
    ExcludeTop: {
      // 参照表格要隐藏的列 field值
      type: Array,
      default: () => [],
    },
    incloudTop: {
      // 包含的列 优先级比隐藏的列高 field值
      type: Array,
      default: () => [],
    },
    ExcludeBottom: {
      // 选中表格要隐藏的列 field值
      type: Array,
      default: () => [],
    },
    defaultNum: {
      type: Number,
      default: 1,
    },
    incloudBottom: {
      // 包含的列 优先级比隐藏的列高 field值
      type: Array,
      default: () => [],
    },
    tableColumnConfig: {
      // 配置的列
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      formStyle: {
        labelWidth: "70px",
      },
      show: true,
      loading: false,
      tableDataTop: [],
      llistlebnth: 0,
      tableData: [],
    };
  },
  methods: {
    handleChange(val) {
      this.params.goods_type_id = val[2];
    },
    onReset() {
      this.$refs.cascader && this.$refs.cascader.clear();
      this.params = this.$publicFution.filter(this.params, this.searchParams);
      this.onSearch();
    },
    onSearch() {
      if (this.$refs.xTable) {
        this.$refs.xTable._getData(1);
      } else {
        this.$nextTick(() => {
          this.$refs.xTable._getData(1);
        });
      }
    },
    selectAllChangeTwo(val) {
      let { selection, reserves } = val;
      this.$nextTick(() => {
        this.llistlebnth = selection.length;
      });
    },
    selectAllChange(val) {
      //全选
      let { selection, reserves } = val;
      this.tableData = reserves.concat(selection);
      this.tableData.forEach((element) => {
        if (!!!element.goods_num) {
          this.$set(element, "goods_num", this.defaultNum);
        }
        this.$refs.xTables._proxy("setSelection", element, true);
      });
      this.$nextTick(() => {
        this.llistlebnth = this.tableData.length;
      });
    },
    submit() {
      let tableCheck = this.$refs.xTables._proxy("getCheckboxRecords");
      if (tableCheck.length) {
        this.$emit("submit", tableCheck);
      } else {
        this.$message({
          message: "请选择商品",
          type: "error",
        });
      }
    },
  },
};
</script>

