// 全局定义过滤器
export default {
  /** 价格保留两位小数 */
  zeroNull(value) {
    if (!value) value = "0";
    return Number(value);
  },
  priceFormatNone(num, precision = 2, separator = ",") { //千分位
    var parts;
    if (!isNaN(parseFloat(num)) && isFinite(num)) {
      num = Number(num);
      num = (typeof precision !== "undefined" ?
        num.toFixed(precision) :
        num
      ).toString();
      parts = num.split(".");
      parts[0] = parts[0]
        .toString()
        .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + (separator || ","));
      return parts.join(".");
    } else if (num == "无权限") {
      return "无权限";
    }
    return num;
  },
  // 转化时间（时分秒）
  getAllTime(time) {
    if (!time) {
      return ''
    }
    if (isNaN(time - 0)) {
      return time
    }
    var result = new Date(time - 0)
    var year = result.getFullYear()
    var month = result.getMonth() + 1
    var day = result.getDate()
    var hours = result.getHours()
    var minuter = result.getMinutes()
    var s = result.getSeconds()
    return `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day} ${hours < 10 ? '0' + hours : hours}:${minuter < 10 ? '0' + minuter : minuter}:${s < 10 ? '0' + s : s}`
  },
  // 转化时间（年月日）
  getTime(time) {
    if (!time) {
      return ''
    }
    if (isNaN(time - 0)) {
      return time
    }
    var result = new Date(time - 0)
    var year = result.getFullYear()
    var month = result.getMonth() + 1
    var day = result.getDate()
    return `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day}`
  },
  priceFormat(s, n) {
    //不能小于0
    if (!s && s !== 0) {
      return s;
    }
    var numberS = Number(s);
    if (numberS && numberS > 0) {
      n = n > 0 && n <= 20 ? n : 2;
      numberS =
        parseFloat((numberS + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
      var l = numberS
        .split(".")[0]
        .split("")
        .reverse(),
        r = numberS.split(".")[1];
      var t = "";
      for (var i = 0; i < l.length; i++) {
        t += l[i] + ((i + 1) % 3 == 0 && i + 1 != l.length ? "," : "");
      }
      if (s < 0 && Math.abs(s) < 1000) {
        return s;
      } else {
        return (
          t
            .split("")
            .reverse()
            .join("") +
          "." +
          r
        );
      }
    } else if (numberS <= 0) {
      return 0;
    } else {
      return null;
    }
  },
  /**审核状态 传审核id 转审核名称 */
  statusFormat(value) {
    if (value == 1) {
      return "未审核";
    } else if (value == 2) {
      return "已审核";
    } else if (value == 3) {
      return "作废";
    } else if (value == 0) {
      return "全部";
    } else if (value == 4) {
      return "关闭";
    } else if(value == 5) {
      return "锁定";
    } else {
      return "未知";
    }
  }
};